import {Component, HostListener, OnInit, ViewChild} from '@angular/core';
import {faLock} from "@fortawesome/free-solid-svg-icons";
import {ContractService} from "../../../services/contract.service";
import {ModalService} from "../../../services/modal.service";
import {LoadingService} from "../../services/loading/loading.service";
import {LocationService} from "../../services/location.service";
import {StoreService} from "../../services/store/store.service";
import {BaseService} from "../../../services/base.service";
import {FormControl, Validators} from "@angular/forms";
import {NotifyService} from "../../../services/notify.service";
import {LangService} from "../../services/lang.service";

@Component({
  selector: 'app-dungeons',
  templateUrl: './dungeons.component.html',
  styleUrls: ['./dungeons.component.sass']
})
export class DungeonsComponent implements OnInit {
  // history
  @ViewChild('paginationBlock') paginationBlock;
  @ViewChild('filterButtons') filterButtons;
  @ViewChild('content') content;

  @HostListener('window:resize', ['$event']) onResize(event) {
    this.updateHistory();
  }

  countOfHistoryItems: number = 0;
  historyPage: number = 1;
  tableHeight: number;
  elements: any = [];


  // Adventure
  icons: any = {
    lock: faLock
  };
  isHistory: boolean = false;


  dungeonsList = [];
  dungeonSelected = 0;


  constructor(private cs: ContractService, private ms: ModalService, private ls: LoadingService, public locationService: LocationService, private store: StoreService, private bs: BaseService, private ns: NotifyService, public lang: LangService) {
    this.dungeonsList = [
      {
        idx: 1,
        type: 'mine',
        time: 0,
        name: this.bs.dungeons[1].name,
        description: this.lang.translate('mineDescription'),
        img: this.bs.dungeons[1].img,
        points: 100,
        food: 1,

        minLevel: 1
      }, {
        idx: 3,
        type: 'farm',
        time: 0,
        name: this.bs.dungeons[3].name,
        description: this.lang.translate('farmDescription'),
        img: this.bs.dungeons[3].img,
        points: 100,
        food: 1,

        minLevel: 2
      }, {
        idx: 2,
        type: 'hell',
        time: 0,
        name: this.bs.dungeons[2].name,
        description: this.lang.translate('hellDescription'),
        img: this.bs.dungeons[2].img,
        points: 300,
        food: 2,

        minLevel: 3
      }, {
        idx: 0,
        type: 'edge',
        time: 0,
        name: this.bs.dungeons[0].name,
        description: this.lang.translate('edgeDescription'),
        img: this.bs.dungeons[0].img,
        points: 1500,
        food: 6,

        minLevel: 5
      },
    ];

    this.dungeonsList.forEach(dungeon => {
      dungeon.time = this.store.loadedData.adventureTimes[dungeon.type];
      dungeon.description += ' ' + this.lang.translate('dungeonSomeDescription');
      dungeon.lock = !(store.loadedData.homeLoad.level >= dungeon.minLevel);
    });


  }

  ngOnInit() {
  }

  /**
   *
   * @param type  - 'history' or 'list'
   */
  changeDungeonViewType(type) {
    this.isHistory = type === 'history';
    this.isHistory && (() => {
      setTimeout(() => {
        this.updateHistory();
      }, 0);
    })();
  }

  changeDungeon(idx) {
    this.dungeonSelected = idx;
  }

  goToAdventure() {
    if (this.dungeonsList[this.dungeonSelected].lock ||
      this.dungeonsList[this.dungeonSelected].food > this.store.loadedData.userInfo.food ||
      this.dungeonsList[this.dungeonSelected].time > this.getCurrentTime()) {
      this.ns.info(this.lang.translate('youCantVisitThisAdventure'));
      return false;
    }
    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate(this.dungeonsList[this.dungeonSelected].type),
        description: `${this.lang.translate('dungeonVisitModalDescription1')} <span class="text-info">${this.bs.declWords({
          number: this.dungeonsList[this.dungeonSelected].food,
          words: this.lang.translate('stakeCounting')
        })}</span>. ${this.lang.translate('dungeonVisitModalDescription2')}`,
        btnText: {
          success: this.lang.translate('visit')
        },
        form: new FormControl(1, [
          Validators.required,
          Validators.min(1),
          Validators.max(10)
        ]),
        callback: (result) => {
          if (result.form.valid === false) {
            if (result.form.errors.min) this.ns.info(this.lang.translate('validateErrorMin1'));
            if (result.form.errors.max) this.ns.info(this.lang.translate('validateErrorMax10'));
            return false;
          }
          if (this.dungeonsList[this.dungeonSelected].food * result.form.value > this.store.loadedData.userInfo.food) {
            this.ns.info(this.lang.translate('youCantVisitThisAdventure'));
            return false;
          }
          this.locationService.goToAdventure('in' + this.dungeonsList[this.dungeonSelected].type, result.form.value);
          modal.close();
        }
      },
    });
  }

  getCurrentTime() {
    return +new Date;
  }

  calcHistoryElements() {
    let contentBorderWidth = parseInt((window.getComputedStyle(<HTMLElement>this.content.nativeElement).borderTopWidth)) + parseInt((window.getComputedStyle(<HTMLElement>this.content.nativeElement).borderBottomWidth));
    let contentPadding = parseInt((window.getComputedStyle(<HTMLElement>this.content.nativeElement).paddingBottom)) + parseInt((window.getComputedStyle(<HTMLElement>this.content.nativeElement).paddingTop));
    let heightOfContent = (<HTMLElement>this.content.nativeElement).getBoundingClientRect().height - contentPadding - contentBorderWidth;

    let heightOfPagination = this.bs.getHeightWithMarginOfElement(this.paginationBlock.nativeElement);


    let heightOfButtons = this.bs.getHeightWithMarginOfElement(this.filterButtons.nativeElement);

    let trHeight = 62;

    this.tableHeight = heightOfContent - heightOfButtons - heightOfPagination;
    this.countOfHistoryItems = Math.ceil(this.tableHeight / trHeight) - 1;
  }

  updateHistory() {
    if (!this.isHistory) return false;
    this.calcHistoryElements();
    if (this.countOfHistoryItems <= 0) return false;
    this.elements = [...Array(this.countOfHistoryItems).keys()].map((item, idx) => {
      idx = (this.countOfHistoryItems * this.historyPage - this.countOfHistoryItems) + idx;
      let history = this.store.loadedData.dungeonHistory[idx];
      if (!history) return undefined;
      history.name = this.bs.dungeons[history.adventureId].name;
      return history;
    });


    console.log(':', this.elements);
  }
}
