import { Injectable } from '@angular/core';
import {SnotifyService} from "ng-snotify";

@Injectable({
  providedIn: 'root'
})
export class NotifyService {
  private config: { position: string };


  constructor(private notify: SnotifyService) {
    this.config = {
      position: "rightTop"
    };
  }

  success(text=null) {
    this.notify.success(text || "!", this.config);
  }


  info(text=null) {
    this.notify.info(text, this.config);
  }


  warning(text=null) {
    this.notify.warning(text || "!", this.config);
  }
}
