import {Injectable} from '@angular/core';
import {ModalService} from "../../../services/modal.service";
import {Subject} from "rxjs";
import {ContractService} from "../../../services/contract.service";
import {NotifyService} from "../../../services/notify.service";
import {Router} from "@angular/router";
import {ItemService} from "../../../services/items/item.service";

@Injectable({
  providedIn: 'root'
})
export class StoreService {

  // Registration Demo data
  isLoadingUserName = false;
  tipId: number = 1;
  nameOfLocalStorageKey: string = 'registrationData';
  nameOfLocalStorageUserFromKey: string = 'userFrom';
  nameOfLocalStorageReferralKey: string = 'ref';
  houseIdSubject = new Subject<any>();
  houseId = this.houseIdSubject.asObservable();
  intervalMessages = null;
  canAddAdMessage: boolean = true;
  houseIdValue = null;
  createItemParam = null;
  paramsOfCreateItems = null;
  results = null; // result after dungeon or open chest and etc
  dressedItems: any = {
    sword: null,
    head: null,
    armour: null,
    legs: null,
    boots: null,
    pickaxe: null,
    hoe: null
  };
  dictionary: any = {};


  clearDressedItems() {
    for (const prop of Object.getOwnPropertyNames(this.dressedItems)) {
      this.dressedItems[prop] = null;
    }
  }

  setHouseId(id) {
    this.houseIdSubject.next(id);
    this.houseIdValue = id;
  }

  // Registration Demo data
  loadedData: any = {
    images: {
      minecart: {},
      backgroundCart: {},
      latestOperationBkg: {},
      referralBkg: {},
      aboutBkg: {},
      topAnimation: {},
      topPlatform: {},
      topStarsBkg: {},
      aboutIcons: {},
      topMainBkg: {}
    },
    homeLoad: null,
    backpackLoad: null,
    dressedItems: null,
    allItems: null,
    userInfo: null,
    durabilityLevelCount: null,
    sounds: null,
    chest: null,
    tradeItemList: null,
    tradeCommission: null,
    dungeonHistory: null,
    adventureTimes: null,
    loadLevelsData: null,
    mainPageEvents: null,
    loadMainInfoForLanding: null,
    partnerInfo: null,
    shop: null,
    housesDemoList: null
  };

  //Game store
  constructor(private ms: ModalService, private cs: ContractService, private ns: NotifyService, private router: Router, private itemService: ItemService) {
    this.createItemParam = itemService.getEmptyItem();
    this.paramsOfCreateItems = itemService.getParamsOfCreate();
    this.intervalMessages = setInterval(() => {
      this.canAddAdMessage = true;
    }, 200000);
  }

  nextTip(data = undefined) {
    if (this.isLoadingUserName) return false;
    switch (this.tipId) {
      case 6: // save it to localStorage
        this.isLoadingUserName = true;
        this.ns.info('Checking if the username is free');
        this.cs.getUserAddrByName(data.name).then((res) => {
          if (res === this.cs.emptyAddr) {
            this.tipId++;
            localStorage.setItem(this.nameOfLocalStorageKey, JSON.stringify(Object.assign({tipId: this.tipId}, data)));
            this.ns.success('Username is available');
          } else {
            this.ns.warning('User with the same name already exists.');
          }
          this.isLoadingUserName = false;
        });
        break;
      case 7:
        //  .
        if (!this.cs.tronWeb) {
          this.ns.info('   TronLink,    ,     ,   ,   ');
        } else if (!this.cs.tronWeb.defaultAddress.base58) {
          //   
          this.ns.info('   TronLink   ');
        } else {
          this.ns.info(' ..  ..');
          let referralUserAddress = localStorage.getItem(this.nameOfLocalStorageReferralKey) || this.cs.emptyAddr;


          this.cs.buyHome({idx: data.home, name: data.name, ref: referralUserAddress}).then(res => {
            this.ns.success();
            this.tipId++;
          });
        }
        break;
      case 8:
        // localStorage.setItem(this.nameOfLocalStorageKey, '');
        // localStorage.setItem(this.nameOfLocalStorageReferralKey, '');

        this.houseIdSubject.next(10);
        this.router.navigate(['game']);
    }

    if (this.tipId < 6) {
      this.tipId++;
      localStorage.setItem(this.nameOfLocalStorageKey, JSON.stringify(Object.assign({tipId: this.tipId}, data)));
    }
  }

  backTip() {
    this.tipId--;
  }


}
