import {Injectable, InjectionToken} from '@angular/core';
import {each, cloneDeep, map, assign} from 'lodash';
import {NotifyService} from "./notify.service";
import {StoreService} from "../game/services/store/store.service";

export const TITLE = new InjectionToken<string>('title', {providedIn: 'root', factory: () => 'title'});


@Injectable({
  providedIn: 'root'
})
export class BaseService {
  directoryPathOfDungeos = 'assets/game/base/dungeons/';
  dungeons = {
    '0': {img: this.directoryPathOfDungeos + 'edge.png', key: 'edge', name: ''},//name is adding in loading.component.ts
    '1': {img: this.directoryPathOfDungeos + 'mine.png', key: 'mine', name: ''},//name is adding in loading.component.ts
    '2': {img: this.directoryPathOfDungeos + 'hell.png', key: 'hell', name: ''},//name is adding in loading.component.ts
    '3': {img: this.directoryPathOfDungeos + 'farm.png', key: 'farm', name: ''},//name is adding in loading.component.ts
  };


  constructor(private notify: NotifyService) {
  }


  getEmptyResourcesValue() {
    return {
      'food': 0,
      'wood': 0,
      'stone': 0,
      'iron': 0,
      'gold': 0,
      'diamond': 0,
    }
  }

  getPropertyIdByKey(key) {
    let prop = {
      'food': -1,
      'wood': 0,
      'stone': 1,
      'iron': 2,
      'gold': 3,
      'diamond': 4,
      'id': 5,

      'loot': 0,
      'damage': 1,
      'defence': 2,
      'durability': 3,

      'end': 0,
      'mine': 1,
      'hell': 2,
      'farm': 3
    };

    if (!prop.hasOwnProperty(key)) {
      console.error("   : " + key);
      return false;
    }
    return prop[key];
  }


  // Item template, for example, you create an item and this method return new objects with params
  getParamsForContract(itemTemplate) {
    let template = cloneDeep(itemTemplate);
    template.craft = this.zipArray(template.craft);
    template.loot = this.zipArray(template.loot);
    template.char = this.zipArray(template.char);
    template.params = this.zipArray(template.params);
    template.armorTypeId = parseInt(template.armorTypeId);
    template.madeTypesId = parseInt(template.madeTypesId);
    template.type = parseInt(template.type);


    return template;
  }

  loadScript(url) {
    return new Promise(resolve => {
      let body = <HTMLDivElement>document.body;
      let script = document.createElement('script');
      script.innerHTML = '';
      script.src = url;
      script.async = true;
      script.defer = true;
      body.appendChild(script);
      script.onload = () => {
        resolve();
      }
    });
  }

  zipArray(arr) {
    return map(arr.sort((i, n) => i.id - n.id), (item: any) => {
      return parseInt(item.value);
    });
  }

  itemUnzip(item) {
    // item.craft [1,2,3,4], loot[] ...
    let template = cloneDeep(item);

    each(['craft', 'loot', 'char', 'params'], type => {
      template[type] = map(this.getItemsCraftParamLootCharArrByKey(type), (typeDataItem: any) => {

        each(template[type], (value, idx) => { // [1,2,3,4]
          if (typeDataItem.id === idx) typeDataItem.value = value;
        });


        return typeDataItem;
      });
    });


    return template; // item.craft [{value, name} ...]
  }


  calculateItem(item) {
    Object.keys(item).forEach((key) => {
      item[key] = convertItem(item[key]);
    });

    function convertItem(item) {
      if (typeof item === "boolean") return item;
      if (item.toNumber) return item.toNumber();
      return item;
    }

    return item;
  }

  getArmorTypeById(id, isObjectKey = false) {
    let obj = {
      'head': 0,
      'armour': 1,
      'leggings': 2,
      'legs': 3
    };

    return isObjectKey ? Object.keys(obj)[id] : obj[id];
  }

  getToolTypeById(id, isObjectKey = false) {
    let obj = {
      'pickaxe': 0,
      'hoe': 1
    };

    return isObjectKey ? Object.keys(obj)[id] : obj[id];
  }

  getMadeTypeById(id, isObjectKey = false) {
    let obj = {
      'wood': 0,
      'stone': 1,
      'iron': 2,
      'gold': 3,
      'diamond': 4,
      'id': 5,
    };


    return isObjectKey ? Object.keys(obj)[id] : obj[id];
  }

  getItemsCraftParamLootCharArrByKey(key) {
    let resources = [
      {name: 'food', type: 'number', value: 0, id: null},
      {name: 'wood', type: 'number', value: 0, id: null},
      {name: 'stone', type: 'number', value: 0, id: null},
      {name: 'iron', type: 'number', value: 0, id: null},
      {name: 'gold', type: 'number', value: 0, id: null},
      {name: 'diamond', type: 'number', value: 0, id: null}
    ];

    let craft = Object.assign([], resources);
    craft.shift();

    let params = {
      craft: craft, // count of [wood, stone, iron, gold, diamond]
      resources,
      loot: [
        {name: 'end', type: 'number', value: 0, id: null},
        {name: 'mine', type: 'number', value: 0, id: null},
        {name: 'hell', type: 'number', value: 0, id: null},
        {name: 'farm', type: 'number', value: 0}
      ], // chance of [end, mine, hell, farm]
      char: [
        {name: 'loot', type: 'number', value: 5, id: null},
        {name: 'damage', type: 'number', value: 5},
        {name: 'defence', type: 'number', value: 5, id: null},
        {name: 'durability', type: 'number', value: 5, id: null},
      ],
      params: [
        {name: 'loot', type: 'number', value: 0, id: null},
        {name: 'damage', type: 'number', value: 0, id: null},
        {name: 'defence', type: 'number', value: 0, id: null},
        {name: 'durability', type: 'number', value: 0, id: null},
      ]
    };


    each(params, parItem => {
      each(parItem, (itemOfPar: any) => {
        itemOfPar.id = this.getPropertyIdByKey(itemOfPar.name);
      });
    });

    return cloneDeep(params[key]);
  }


  getRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  redYellowGreen(min, max, value) {
    let green_max = 220;
    let red_max = 220;
    let red = 0;
    let green = 0;
    let blue = 0;

    if (value < max / 2) {
      red = red_max;
      green = Math.round((value / (max / 2)) * green_max);
    } else {
      green = green_max;
      red = Math.round((1 - ((value - (max / 2)) / (max / 2))) * red_max);
    }

    let to_return = {
      red: 0,
      green: 0,
      blue: 0
    };
    to_return.red = red;
    to_return.green = green;
    to_return.blue = blue;

    return `rgb(${red}, ${green}, ${blue})`;
  }


  getHeightWithMarginOfElement(element: HTMLElement) {
    let marginTop = parseInt(window.getComputedStyle(element).marginTop);
    let marginBottom = parseInt(window.getComputedStyle(element).marginBottom);
    let heightOfElement = (<HTMLElement>element).getBoundingClientRect().height;
    return marginTop + marginBottom + heightOfElement;
  }

  splitIdsAndReturnArray(list) {
    return [...list.split('|')].filter(id => id !== '').map(id => parseInt(id))
  }

  /**
   * {number: 10, words: ['', '', '']}
   * @param input
   */
  declWords(input) {
    let number = input.number;
    let after = input.words;
    let cases = [2, 0, 1, 1, 1, 2];
    return number + ' ' + after[(number % 100 > 4 && number % 100 < 20) ? 2 : cases[Math.min(number % 10, 5)]];
  }

  copyText(text: string) {
    let selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = text;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
    this.notify.success('  ');
  }

  scrollTo(to) {
    to.scrollIntoView();
  }
}
