import {Component, ElementRef, HostListener, OnInit, ViewChild, OnDestroy} from '@angular/core';
import {animate, style, transition, trigger} from "@angular/animations";
import {ContractService} from "../../../services/contract.service";
import {StoreService} from "../../services/store/store.service";
import {LocationService} from "../../services/location.service";
import {ModalService} from "../../../services/modal.service";
import {NotifyService} from "../../../services/notify.service";
import {BaseService} from "../../../services/base.service";

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.sass'],
  animations: [
    trigger('fadeInOut', [
      transition(':enter', [   // :enter is alias to 'void => *'
        style({opacity: 0}),
        animate(100, style({opacity: 1}))
      ]),
      transition(':leave', [   // :leave is alias to '* => void'
        animate(300, style({opacity: 0}))
      ])
    ])
  ]

})
export class ChatComponent implements OnInit, OnDestroy {
  @ViewChild('gameChat') messageElement: ElementRef;
  expiredTime: number = 9000;
  isOpened: boolean = false;
  isOpenedType: boolean = false;
  interval: number;
  intervalMessages: number;
  destroyed: boolean = false;
  canAddAdMessage: boolean = true;
  adMessageList: any = [
    '     ,      .',
    '    .      . <a href="https://t.me/goldencraftio" target="_blank">  </a>',
    '     ,   !   ',
    '    <a href="https://t.me/joinchat/LOdd8BCUyjDUyCtAGx8FlA" target="_blank"></a>',
  ];
  messages: any = [];

  @HostListener('document:keydown', ['$event']) onKeydownHandler(event: KeyboardEvent) {
    switch (event.key.toLowerCase()) {
      case 'escape':
        this.closeChat();
        break;
      case 't':
      case '/':
      case '.':
      case '':
        this.openChat();
        setTimeout(() => {
          this.messageElement.nativeElement.focus();
        }, 0);
        // open chat
        break;
      case 'enter':
        if (this.isOpenedType) {
          let strValue = this.messageElement.nativeElement.value;
          if (strValue.length > 0) {
            let dataMessage = strValue.charAt(0) === '"' ? this.getUserNameFromMessage(strValue) : false;
            let userName = dataMessage && dataMessage.name;
            let msg = dataMessage && dataMessage.msg;
            let isTorg = strValue.charAt(0) === '!';
            (async () => {
              let addrUserByName = userName ? await this.cs.getUserAddrByName(userName) : null;
              let message = userName ? msg : strValue;
              if (addrUserByName !== null && addrUserByName === this.cs.emptyAddr) {
                this.notify.warning('      ' + userName);
                return false;
              }
              if (!message) {
                this.notify.warning('     ');
                return false;
              }

              let messageType = userName ? 2 : isTorg ? 1 : 0;
              if (isTorg) message = message.substr(1);
              this.cs.createMessage(message, messageType, addrUserByName).then(res => {

                messageType === 2 && (() => {
                  let message = {
                    active: true,
                    messageType: 3,
                    senderName: 'ME',
                    message: "   "
                  };
                  this.messages.push(message);
                  this.removeMessageAfterSomeTime(message);
                })()
              }).catch(err => {

              });
              this.closeChat();
            })();
          }
        }
        break;
    }
  }

  getUserNameFromMessage(strValue) {
    let message = strValue.match(/[\w-]+|"[^"]&.*"/g);
    return {
      name: message.shift().replace(/['"]+/g, ''),
      msg: message.join(' ').trim()
    };
  }

  constructor(private cs: ContractService, private store: StoreService, private locationService: LocationService, private modalService: ModalService, private notify: NotifyService, private bs: BaseService) {
  }

  ngOnInit() {
    this.loadMessages();
    this.interval = setInterval(() => {
      this.loadMessages();
    }, 2000);

  }

  ngOnDestroy() {
    clearInterval(this.interval);
    this.destroyed = true;
  }


  closeChat() {
    this.isOpened = false;
    this.isOpenedType = false;
    this.locationService.elements.chat = false;
  }

  loadMessages() {
    this.cs.getMessagesList(10, -1).then((messages: any) => {
      let msgList = this.messages.map(msg => msg.time);
      this.messages.length && messages.forEach(msg => {
        if (this.store.canAddAdMessage) {
          let adMessage = this.createRandomMessage();
          this.messages.push(adMessage);
          this.store.canAddAdMessage = false;
          this.removeMessageAfterSomeTime(adMessage);
        }
        if (msgList.indexOf(msg.time) === -1) {
          msg.active = true;
          this.messages.push(msg);
          this.removeMessageAfterSomeTime(msg);
        }
      });
      if (!this.messages.length) this.messages = messages;
    });
  }

  openChat() {
    this.isOpened = true;
    this.isOpenedType = true;
    this.locationService.elements.chat = true;
  }

  createRandomMessage() {
    return {
      active: true,
      messageType: 3,
      senderName: '',
      message: this.adMessageList[this.bs.getRandomInt(0, this.adMessageList.length - 1)]
    };
  }

  sendPrivateMessage(msg: any) {
    if (!msg.sender) return false;
    this.openChat();
    setTimeout(() => {
      let currentMsg = this.messageElement.nativeElement.value;
      let user = '"' + msg.senderName + '" ';
      this.messageElement.nativeElement.value = user + currentMsg;
      this.messageElement.nativeElement.focus();
    }, 0);
  }

  removeMessageAfterSomeTime(msg) {
    setTimeout(() => {
      if (this.destroyed) return false;
      msg.active = false;
    }, this.expiredTime)
  }


}
