import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {StoreService} from "../../../services/store/store.service";
import {HttpClient} from "@angular/common/http";
import {debounce, debounceTime, first, throttleTime} from "rxjs/operators";
import {ModalService} from "../../../../services/modal.service";
import {LocationService} from "../../../services/location.service";
import {LoadingService} from "../../../services/loading/loading.service";
import {ContractService} from "../../../../services/contract.service";
import {BaseService} from "../../../../services/base.service";
import {isUndefined} from "util";
import {fromEvent} from "rxjs";
import {LangService} from "../../../services/lang.service";


@Component({
  selector: 'app-sphere',
  templateUrl: './sphere.component.html',
  styleUrls: ['./sphere.component.sass'],
})
export class SphereComponent implements OnInit, OnDestroy {
  @Input() sphereHasBeenLoaded;
  @Input() sphereType;
  markersData = null;
  latestLocationId = null;
  positionUpdate = null;
  sphere = null;
  maps = null;
  canChangeLocation: boolean = true;

  constructor(private store: StoreService, private http: HttpClient, private ms: ModalService, private locationService: LocationService, private ls: LoadingService, private cs: ContractService, private bs: BaseService, private lang: LangService) {
  }

  ngOnInit() {
    this.loadScript();
  }


  drawLocation(fistLoad = false) {
    //      .
    //  assets/game/maps/{house_id}/{level_house_id}/maps.json - for game
    let mapUrl = this.sphereType === 'demo' ? 'assets/game/data/maps.json' : `assets/game/maps/${this.store.loadedData.homeLoad.idx}/${this.store.loadedData.homeLoad.level}/maps.json`;
    this.http.get(mapUrl).subscribe((maps) => {
      this.maps = maps;
      let locationFromLocalStorage = localStorage.getItem('goldenCraftLocation');
      this.latestLocationId = locationFromLocalStorage && maps[locationFromLocalStorage] ? locationFromLocalStorage : this.latestLocationId ? this.latestLocationId : 'athome';
      let scene = this.sphereType === 'demo' ? maps[this.latestLocationId].images[0] + '0.png' : maps[this.latestLocationId].images[0];

      if ((this.store.houseIdValue !== null && !isUndefined(this.store.houseIdValue)) && this.sphereType === 'demo') {
        // i have home id and i need to paste it here
        scene = maps[this.latestLocationId].images[1] + this.store.houseIdValue + '/1/' + this.latestLocationId + '/0.png';
      }

      this.setNewPanorama(scene, this.latestLocationId);
    });
  }


  setNewPanorama(scene, id) {
    this.sphere.clearMarkers();
    this.sphere.setPanorama(scene).then((res) => {
      setTimeout(() => {
        this.sphere.zoom(0);
        this.sphereHasBeenLoaded();
      }, 0);

      //     .
      this.maps[id].markers.forEach(mrk => {
        this.addMarkers(mrk)
      });
    });

  }

  addMarkers(marker) {
    let markersAvailableTypes = {
      "sphere-move-point": "move",
      "sphere-open-point": "open",
      "sphere-dungeon-point": "toComeIn"
    };
    if (typeof marker === 'string') {
      this.markersData[marker].forEach(mrk => {
        this.addMarkers(mrk);
      });
    } else {
      marker.html = this.lang.translate(marker.key);
      marker.html += `<span class="marker-action-type">${this.lang.translate(markersAvailableTypes[marker.className])}</span>`;
      this.sphere.addMarker(marker);
    }
  }


  goToAdventure(type) {
    this.locationService.goToAdventure(type);
  }

  openSomething(type) {
    switch (type) {
      case 'chest':
        // open chest
        this.locationService.openChest();
        break;
      case 'craftingTable':
        // open craftingTable
        this.locationService.toggle('craftingTable');
        break;
    }
  }

  eventsTrigger() {
    let onClickToSphere = (e) => {
      if (!this.sphere.getMarker) return false;
      // click on markers
      let element = e.target;
      if (!element.classList.contains('sphere-move-point') && !element.classList.contains('sphere-open-point') && !element.classList.contains('sphere-dungeon-point')) return false;
      let marker = this.sphere.getMarker(element.id.split('psv-marker-')[1]);


      if (element.classList.contains('sphere-open-point')) {
        // we are trying to open something
        this.openSomething(marker.id);
        return false;
      }


      if (element.classList.contains('sphere-dungeon-point')) {
        // we are trying to open something
        this.goToAdventure(marker.id);
        return false;
      }

      this.latestLocationId = marker.id;
      setTimeout(() => {
        this.sphereHasBeenLoaded(true);
      }, 200);

      // this.sphere.animate({
      //   latitude: marker.latitude,
      //   longitude: marker.longitude,
      //   zoom: 40,
      // }, 300);


      setTimeout(() => {

        if (this.store.tipId == 3) { //     !!!
          setTimeout(() => {
            this.store.nextTip();
          }, 1500);
          // return false;
        }
        localStorage.setItem('goldenCraftLocation', this.latestLocationId);
        this.drawLocation();
      },350);


    };

    document.addEventListener('click', onClickToSphere, true);

    window.onresize = (e: Event) => {
      this.sphere.resize({height: window.innerHeight + 'px'});
    };

    // this.sphere.on('click', (e) => {
    //   this.sphere.addMarker({
    //     id: '#' + Math.random(),
    //     longitude: e.longitude,
    //     latitude: e.latitude,
    //     scale: [0.4, 1.3],
    //     html: e.longitude + '|' + e.latitude,
    //     className: 'sphere-move-point',
    //     anchor: 'bottom center',
    //     data: {
    //       generated: true
    //     }
    //   });
    //
    //   let copiedObj = {
    //     "id": "CHangeit",
    //     "longitude": e.longitude, "latitude": +e.latitude,
    //     "html": "",
    //     "scale": [
    //       0.35,
    //       1.3
    //     ],
    //     "className": "sphere-move-point"
    //   };
    //   this.bs.copyText(JSON.stringify(copiedObj));
    // });


    this.sphere.once('ready', () => {
      let positionOfSphere = localStorage.getItem('goldenCraftPositionSphere') && JSON.parse(localStorage.getItem('goldenCraftPositionSphere')) || {
        latitude: 0,
        longitude: 0
      };

      this.sphere.rotate({
        latitude: positionOfSphere.latitude,
        longitude: positionOfSphere.longitude,
      });
      this.sphere.zoom(0);
      this.positionUpdate = fromEvent(this.sphere, 'position-updated').pipe(throttleTime(200)).subscribe(e => {
        if (isNaN(e[0].latitude) || isNaN(e[0].longitude))  return false;
        localStorage.setItem('goldenCraftPositionSphere', JSON.stringify(e[0]));
      });

      this.sphereHasBeenLoaded();
    });

    this.store.houseId.subscribe(value => {
      //   ,     ,   

      this.sphereHasBeenLoaded(true);// turn on loading
      this.drawLocation();
    });

  }

  loadScript() {
    this.bs.loadScript('assets/libs/sphereViewer.js').then(() => {
      // @ts-ignore
      this.sphere = new PhotoSphereViewer({
        container: 'sphere',
        panorama: null,
        loading_txt: ' ',
        cache_texture: 10,
        time_anim: false,
        size: {
          height: window.innerHeight
        },
        navbar: false,
        markers: {},
      });

      this.http.get('assets/game/data/markers.json').subscribe(markers => {
        this.markersData = markers;
        this.eventsTrigger();
        this.drawLocation(true);
      })
    });
  }

  ngOnDestroy() {
    window.onresize = null;
    this.sphere.off('click');
    this.sphere.off('position-updated');
    this.positionUpdate.unsubscribe();
    this.sphere.destroy();
  }
}
