import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {NotifyService} from "../../../services/notify.service";

@Component({
  selector: 'app-thank-and-claim',
  templateUrl: './thank-and-claim.component.html',
  styleUrls: ['./thank-and-claim.component.sass']
})
export class ThankAndClaimComponent implements OnInit {
  @Input() type;
  @Input() to;
  @Output() updateCallback = new EventEmitter();

  description: string = "";


  maxLengthOfTextarea = 150;

  countOfTrxModel = 50;
  countOfTrx: any = [
    {count: 50},
    {count: 100},
    {count: 200},
    {count: 500},
    {count: 1000},
    {count: 10000},
    {count: 50000},
    {count: 100000}
  ];


  constructor(private cs: ContractService, private ns: NotifyService) {


  }

  ngOnInit() {

  }

  thankUser() {
    this.cs.thankUser(this.to, this.description, this.countOfTrxModel).then(res => {
      this.description = "";
      this.ns.success();
      this.updateCallback.emit();
    });
  }

  claimUser() {
    this.cs.claimUser(this.to, this.description).then(res => {
      this.description = "";
      this.ns.success();
      this.updateCallback.emit();
    });
  }
}
