import {Component, OnInit} from '@angular/core';
import {StoreService} from "../../services/store/store.service";
import {faCaretRight, faCaretLeft} from "@fortawesome/free-solid-svg-icons"
import {ContractService} from "../../../services/contract.service";
import {LoadingService} from "../../services/loading/loading.service";
import {ModalService} from "../../../services/modal.service";
import {ItemService} from "../../../services/items/item.service";
import {NotifyService} from "../../../services/notify.service";
import {FormControl, Validators} from "@angular/forms";
import {LocationService} from "../../services/location.service";
import {LangService} from "../../services/lang.service";


@Component({
  selector: 'app-shop',
  templateUrl: './shop.component.html',
  styleUrls: ['./shop.component.sass']
})
export class ShopComponent implements OnInit {
  icons = {faCaretRight, faCaretLeft};
  chestList: any = [];
  selectedChest = 0;

  constructor(private store: StoreService, private cs: ContractService, private ls: LoadingService, private ms: ModalService, private itemService: ItemService, private ns: NotifyService, public locationService: LocationService, private lang: LangService) {
    Object.keys(this.itemService.nameAndBkgPosOfItem.chest).forEach(key => {
      this.chestList.push(Object.assign({}, this.itemService.nameAndBkgPosOfItem.chest[key]));
    });
  }

  slideChest(id) {
    if (id < 0 || id >= this.chestList.length) return false;
    this.selectedChest = id;
  }

  buyChest() {
    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('boughtChest'),
        btnText: {success: this.lang.translate('buy'), cancel: this.lang.translate('close')},
        description: this.lang.translate('buyChestModalDescription'),
        callback: () => {
          this.ls.showLoadWithText(this.lang.translate('pleaseWait'));
          this.cs.buyChest(this.chestList[this.selectedChest].id).then(res => {
            this.ns.success(this.lang.translate('successUpdateUserData'));
            this.ls.isLoad = false;
          });

          modal.close();
        }
      },
    });
  }

  ngOnInit() {
    this.chestList = this.chestList.map((chest, idx) => {
      chest = Object.assign(chest, this.store.loadedData.shop[idx]);
      return chest
    });
  }

}
