import {Component, Input, OnInit} from '@angular/core';
import {faDownload, faInfoCircle, faTimes, faUndo} from "@fortawesome/free-solid-svg-icons";
import {ModalService} from "../../../services/modal.service";
import {ContractService} from "../../../services/contract.service";
import {NotifyService} from "../../../services/notify.service";

@Component({
  selector: 'app-icons',
  templateUrl: './icons.component.html',
  styleUrls: ['./icons.component.sass']
})
export class IconsComponent implements OnInit {
  @Input() type;
  @Input() data;

  faInfoCircle: any = faInfoCircle;
  faDownload: any = faDownload;
  faTimes: any = faTimes;
  faUndo: any = faUndo;

  constructor(private modalService: ModalService, private cs: ContractService, private ns: NotifyService) {
  }

  ngOnInit() {
  }

  showMoreInfo(addr) {
    this.modalService.moreInfoAboutUser(addr);
  }

  loadInformationAboutBannedMessage(data) {
    this.cs.contract.RemovedMessagesList(this.data.messageId, this.data.typeId).call().then(message => {
      data.message = message.message;
    });
  }

  changeAccessModer(status) {
    this.cs.contract.updateModerStatus(this.data, status).send(this.cs.getOptionsWithPrice(0)).then(res => {
      this.ns.success();
    });
  }




}
