import {Component, OnInit} from '@angular/core';
import {ContractService} from "../../../services/contract.service";

@Component({
  selector: 'app-bonus',
  templateUrl: './bonus.component.html',
  styleUrls: ['./bonus.component.sass']
})
export class BonusComponent implements OnInit {
  smartContractAddress: string;
  newContractService: any;
  userList: any = [];
  models: any = {
    chest: {
      addr: '',
      id: 0,
      disabled: false
    },
    ref: {
      addr: '',
      percent: 10
    }
  };

  constructor(private cs: ContractService) {

  }

  ngOnInit() {
    this.smartContractAddress = 'TKdpQpy89SbUc56znhZba7Zv2oDdnG76YR'; //  ,         
    // this.newContractService = new ContractService()
    this.loadDataFromContract();
  }

  loadDataFromContract() {
    this.userList = [];
    this.cs.isContractExist(() => {
      let currentContract = Object.assign({}, this.cs.contract);
      this.cs.trxWithoutWallet.contract().at(this.smartContractAddress).then(async (oldContract) => {
        this.cs.contract = oldContract;
        let oldUsers: any = await this.cs.loadUsers(0, 100);
        this.cs.contract = currentContract;
        let newUsers: any = await this.cs.loadUsers(0, 100);
        let list = [];
        oldUsers.users.forEach((oldUser: any) => {
          let isUniq = true;
          newUsers.users.forEach((newUser: any) => {
            if (oldUser.addr === newUser.addr) isUniq = false;
          });

          if (isUniq) list.push(oldUser);
        });
        this.userList = list;
      });
    })

  }

  regUser(user) {
    this.userList = [];
    console.log('user:', user);
    // @ts-ignore
    console.log('tronWeb.address.fromHex(user.info.addr):', tronWeb.address.fromHex(user.info.addr));
    // @ts-ignore
    this.cs.regUser(tronWeb.address.fromHex(user.addr), user.info.name, user.house.idx).then(() => {
      this.loadDataFromContract()
    });
  }

  makeChestGift() {
    this.models.chest.disabled = true;
    this.cs.giftUserChest(this.models.chest.id, this.models.chest.addr).then(() => {
      this.models.chest.disabled = false;
    });
  }

  updateRefPercent() {
    this.models.ref.disabled = true;
    this.cs.updateReferralPercentOfUser(this.models.ref.percent, this.models.ref.addr).then(() => {
      this.models.ref.disabled = false;
    });
  }
}
