import {Component, OnInit} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {faCopy} from "@fortawesome/free-solid-svg-icons/faCopy";
import {BaseService} from "../../../services/base.service";
import {NotifyService} from "../../../services/notify.service";
import {ModalService} from "../../../services/modal.service";
import {LoadingService} from "../../services/loading/loading.service";
import {StoreService} from "../../services/store/store.service";
import {LocationService} from "../../services/location.service";
import {LangService} from "../../services/lang.service";

@Component({
  selector: 'app-referral',
  templateUrl: './referral.component.html',
  styleUrls: ['./referral.component.sass']
})
export class ReferralComponent implements OnInit {
  icons = {
    faCopy
  };
  link: string = 'https://goldencraft.io/#/?ref=';

  constructor(private cs: ContractService, public bs: BaseService, private ms: ModalService, private ls: LoadingService, public store: StoreService, private ns: NotifyService, public locationService: LocationService, private lang: LangService) {
  }

  ngOnInit() {
    this.link += this.cs['tronWeb'].defaultAddress.hex;
  }

  showMoreInfo() {
    this.ms.moreInfoAboutUser(this.cs['tronWeb'].defaultAddress.base58);
  }

  toStayPartner() {
    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('referralProgram'),
        description: this.lang.translate('costOfPartnerProgram1') + ' <span class="text-info">' + this.store.loadedData.partnerInfo.price + ' TRX</span> ' + this.lang.translate('costOfPartnerProgram2') + ' <span class="text-info">' + this.store.loadedData.partnerInfo.percent + '%</span> ' + this.lang.translate('continue') + '?',
        callback: (result) => {
          this.ls.showLoadWithText(this.lang.translate('waitPlease'));
          this.cs.buySuperInvestor().then(res => {
            this.ls.loadByArrayType(['userInfo']);
            this.ns.success(this.lang.translate('successUpdateUserData'));
          }).catch(err => {
            this.ls.loadByArrayType(['userInfo']);
          });
          modal.close();
        }
      },
    });
  }
}
