import {Component, OnInit} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {faRegistered} from '@fortawesome/free-solid-svg-icons';
import {timer} from "rxjs";
import {LangService} from "../../../game/services/lang.service";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.sass']
})
export class HeaderComponent implements OnInit {

  isAdmin: any = false;
  isReg: any = false;
  faRegistered = faRegistered;

  constructor(private cs: ContractService) {
  }

  ngOnInit() {
    (async () => {
      this.isRegistered();

      timer(5000, 5000).subscribe(() => {
        this.isRegistered();
      })
    })();
  }

  isRegistered() {
    (async () => {
      this.isAdmin = await this.cs.isUserAdmin();
      this.isReg = await this.cs.userInfo();
      this.isReg = this.isReg.idx;
    })();
  }

}
