import {Injectable} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {StoreService} from "../store/store.service";
import {ItemService} from "../../../services/items/item.service";
import {HomeParams} from "../../../interfaces/home-params";
import {HttpClient} from "@angular/common/http";
import {NotifyService} from "../../../services/notify.service";
import set = Reflect.set;
import {LangService} from "../lang.service";

@Injectable({
  providedIn: 'root'
})
export class LoadingService {
  title: string;
  isLoad: boolean = true;
  availableTypes = null;

  constructor(private cs: ContractService, private store: StoreService, private itemService: ItemService, private http: HttpClient, private ns: NotifyService, private lang: LangService) {
    this.title = this.lang.loadingComponentDictionary['connectToSmartContract'][this.lang.getCurrentLang()];
    this.availableTypes = {
      'userInfo': {
        title: this.lang.loadingComponentDictionary['loadDataOfUser'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.userInfo().then(resolve).catch(reject)
          })
        }
      },
      'shop': {
        title: this.lang.loadingComponentDictionary['loadDataFromStore'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.loadAllChests().then(resolve).catch(reject)
          })
        }
      },
      'homeLoad': {
        title: this.lang.loadingComponentDictionary['loadDataOfHome'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.getUserHouse().then(resolve).catch(reject)
          })

        }
      },
      'loadMainInfoForLanding': {
        title: this.lang.loadingComponentDictionary['loadMainProjectInfo'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.loadMainInfoForLanding().then(resolve).catch(reject)
          })
        }
      },
      'mainPageEvents': {
        title: this.lang.loadingComponentDictionary['loadDataOfLatestOperations'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.getLatestOperations().then(resolve);
          })
        }
      },
      'backpackLoad': {
        title: this.lang.loadingComponentDictionary['loadInventory'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.loadUserItems(undefined, this.itemService.getItemTypeUseById('backpack')).then(resolve).catch(reject)
          })
        }
      },
      'chestItemLoad': {
        title: this.lang.loadingComponentDictionary['chestItemLoad'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.loadAllUserChests().then((data: any) => {
              data.forEach(chest => chest.isChest = true);
              resolve(data);
            }).catch(reject);
          })
        }
      },
      'loadLevelsData': {
        title: this.lang.loadingComponentDictionary['loadHouseInfo'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.loadLevelsData().then(resolve).catch(reject)
          })
        }
      },

      'partnerInfo': {
        title: this.lang.loadingComponentDictionary['loadPartnerInfo'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.getSuperInvestorInfo().then(resolve).catch(reject)
          })
        }
      },
      'chest': {
        title: this.lang.loadingComponentDictionary['loadItemsFromChest'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.loadUserItems(undefined, this.itemService.getItemTypeUseById('chest'), 0, 270).then(resolve).catch(reject)
          })
        }
      },
      'dressedItems': {
        title: this.lang.loadingComponentDictionary['loadUserItems'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.loadUserItems(undefined, this.itemService.getItemTypeUseById('dressed')).then((res: any) => {
              this.store.clearDressedItems();
              res.forEach(item => {
                if (item.type === 0) this.store.dressedItems.sword = item;
                // armours
                if (item.type === 1 && item.armorTypeId == 0) this.store.dressedItems.head = item;
                if (item.type === 1 && item.armorTypeId == 1) this.store.dressedItems.armour = item;
                if (item.type === 1 && item.armorTypeId == 2) this.store.dressedItems.legs = item;
                if (item.type === 1 && item.armorTypeId == 3) this.store.dressedItems.boots = item;
                // Tools
                if (item.type === 2 && item.toolTypeId === 0) this.store.dressedItems.pickaxe = item;
                if (item.type === 2 && item.toolTypeId === 1) this.store.dressedItems.hoe = item;
              });

              resolve(res);
            }).catch(reject)
          })

        }
      },
      'allItems': {
        title: this.lang.loadingComponentDictionary['loadAllItems'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.loadAvailableItems().then(resolve).catch(reject)
          })

        }
      },
      'adventureTimes': {
        title: this.lang.loadingComponentDictionary['loadAdventureInfo'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.loadAdventureTimes().then(resolve).catch(reject)
          })
        }
      },
      'dungeonHistory': {
        title: this.lang.loadingComponentDictionary['loadAdventureInfo'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.getHistoryOfAdventure().then(resolve).catch(reject)
          })
        }
      },
      'durabilityLevelCount': {
        title: this.lang.loadingComponentDictionary['loadMainProjectInfo'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.durabilityLevelCount().then(resolve).catch(reject)
          })

        }
      },
      'tradeItemList': {
        title: this.lang.loadingComponentDictionary['loadTradeItems'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.getTradeItemsId().then(resolve).catch(reject);
          });
        }
      },
      'tradeCommission': {
        title: this.lang.loadingComponentDictionary['loadMainProjectInfo'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.loadTradeCommissionPrice().then(res => {
              resolve(this.cs.calcTrx(res, true))
            }).catch(reject);
          });
        }
      },
      'housesDemoList': {
        title: this.lang.loadingComponentDictionary['loadHouseInfo'][this.lang.getCurrentLang()],
        promise: () => {
          return new Promise((resolve, reject) => {
            this.cs.isContractExist(() => {
              this.http.get('assets/game/data/demo-houses-cards.json').subscribe(houseDataFromJson => {
                this.cs.loadAvailableHouses().then((houses: HomeParams[]) => {
                  let housesList = houses.map(home => {
                    home.jsonData = houseDataFromJson[home.idx];
                    home.discountPercent = Math.round(100 - (home.price / home.jsonData.price * 100));
                    return home;
                  });
                  console.log('housesList:', housesList);
                  resolve(housesList);
                });
              });
            })
          })
        }
      },
    };

  }


  loadByType(type) {
    this.title = this.availableTypes[type].title;
    return new Promise((resolve, reject) => {
      this.availableTypes[type].promise().then(res => {
        this.store.loadedData[type] = res;
        resolve();
      }).catch(err => {
        reject();
      });
    });
  }


  loadImages(images) {
    return new Promise((resolve, reject) => {
      let promises = [];
      Array.isArray(images) && images.forEach(image => {
        promises.push(new Promise(resolve => {
          let img = new Image();
          img.src = image.path;
          img.onload = () => {
            this.store.loadedData.images[image.name] = img;
            resolve();
          }
        }));
      });

      Promise.all(promises).then(() => {
        resolve();
      });
    });

  }


  loadByArrayType(list, callback = undefined, withFreeze = false) {
    setTimeout(() => {
      let promises = [];
      list.forEach(listItem => {
        promises.push(new Promise((resolve, reject) => {
          this.loadByType(listItem).then(resolve);
        }));
      });

      Promise.all(promises).then(values => {
        this.isLoad = false;
        callback && callback();
      });
    }, withFreeze ? 4000 : 0);
  }

  showLoadWithText(text: string = undefined) {
    this.title = text || this.lang.translate('pleaseWait');
    this.isLoad = true;
  }

}
