import {Component, OnInit} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {Items} from "../../../interfaces/items";
import {BaseService} from "../../../services/base.service";
import {NotifyService} from "../../../services/notify.service";
import {map, uniqBy} from "lodash";
import {HttpClient} from "@angular/common/http";
import {ItemService} from "../../../services/items/item.service";
import {Spinner} from "../../../interfaces/spinner";


@Component({
  selector: 'app-items',
  templateUrl: './items.component.html',
  styleUrls: ['./items.component.sass'],
  providers: [BaseService]
})
export class ItemsComponent implements OnInit {
  items: Items[] = [];
  itemList = [];
  createItemParam: any = [];
  paramsOfCreateItems: any = [];
  selectable: any;

  templates: any = [];

  collapse: any = {
    backpack: false,
    availableItems: false,
    createItem: false,
  };
  tradePrice: any;

  constructor(public cs: ContractService, private bs: BaseService, private notify: NotifyService, private http: HttpClient, public itemService: ItemService) {

    this.createItemParam = itemService.getEmptyItem();
    this.paramsOfCreateItems = itemService.getParamsOfCreate();
    this.selectable = itemService.getSelectableItems();
    this.selectable.madeTypes = map(this.selectable.madeTypes, (type: any) => {
      return Object.assign({id: bs.getPropertyIdByKey(type.key)}, type);
    });

  }


  ngOnInit() {
    this.http.get('assets/items.json').subscribe((res) => {
      this.templates = res;
      this.cs.loadAvailableItems().then((res: any) => {
        this.itemList = res;
      });
    });
    this.loadUserItems();
    setInterval(() => {
      // this.itemCraft(0);
    }, 3000)
  }

  loadUserItems() {
    this.cs.loadUserItems().then((items: any) => {
      this.items = items;
    });
  }

  removeItem(id) {
    this.cs.removeItem(id).then(res => {
      this.loadUserItems();
    });
  }

  changeUseTypeItem(id, type) {
    this.cs.changeUseTypeItem(id, type).then(res => {
      this.loadUserItems();
    });
  }

  trowOutItem(id) {
    this.cs.trowOutItem(id).then(res => {
      this.loadUserItems();
    });
  }

  paramsClear(item) {
    this.createItemParam.params.forEach(item => {
      item.available = false;
    });
    item.available = true;
  }

  copyToClipboard(item) {
    let data = {
      name: this.createItemParam.name,
      type: item.type,
      loot: item.loot,
      craft: item.craft,
      char: item.char,
      params: item.params,
      madeTypesId: item.madeTypesId,
      armorTypeId: item.armorTypeId,
      toolTypeId: item.toolTypeId,
    };

    let text = JSON.stringify(data);
    this.bs.copyText(text);
    this.notify.success(' !');
  }

  loadTemplate(template) {
    this.createItemParam = this.itemService.getEmptyItem(template);
    this.collapse.createItem = true;
    this.notify.success('  ');
  }

  addItemToUser(id) {
    this.cs.addItemToUser(id).then(res => {
      this.loadUserItems();
      this.notify.success();
    });
  }

  updateItem(template, id) {
    this.cs.updateItem(template, id).then(res => {
      this.loadUserItems();
    });
  }

  uploadTemplate(template) {
    this.cs.uploadItemTemplate(template).then((res: any) => {
      this.loadUserItems();
    });
  }

  itemCraft(id) {
    this.cs.itemCraft(id).then(res => {
      // this.loadUserItems();
    });
  }

  repairItem(id) {
    this.cs.repairItem(id).then(res => {
      this.loadUserItems();
    })

  }

  tradeItem(id, price) {
    this.cs.tradeItem(id, price).then(res => {
      this.loadUserItems();
    });
  }

}
