import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {StoreService} from "./store/store.service";

@Injectable({
  providedIn: 'root'
})
export class LangService {
  loadingComponentDictionary: any = {};
  langIdxByLang: any = {};
  userLang: any = localStorage.getItem('lang') || 'en';

  constructor(private http: HttpClient, private store: StoreService) {
    this.langIdxByLang = {'ru': 0, 'en': 1, 'china': 2};

    this.loadingComponentDictionary = {
      loading: this.getObjectByLang(',  ', 'Loading please wait', ''),
      tronLink: this.getObjectByLang(' TronLink  ,     ,   .', 'TronLink extension is not installed, in order to start the game, you must install the extension.', 'TronLink'),
      needAuth: this.getObjectByLang('    TronLink', 'You must log in to the TronLink extension', 'TronLink'),
      tronLinkTest: this.getObjectByLang(',      TronLink.', 'Checking if you have the TronLink extension installed.', 'TronLink'),
      imageLoading: this.getObjectByLang('   ', 'Loading animations and images', ''),
      loadLibrary: this.getObjectByLang('   ', 'Download scripts and libraries', ''),
      loadReference: this.getObjectByLang(' ', 'Loading reference', ''),
      auth: this.getObjectByLang('  TronLink', 'Log in to TronLink', 'TronLink'),
      loadLocation: this.getObjectByLang(' ..', 'Loading location..', '..'),
      connectToSmartContract: this.getObjectByLang('  ', 'Connect to a smart contract', ''),
      loadDataOfUser: this.getObjectByLang('  ', 'Loading user data', ''),
      loadDataFromStore: this.getObjectByLang('   ', 'Loading data from the store', ''),
      loadDataOfHome: this.getObjectByLang('   ', 'Loading data of your home', ''),
      loadMainProjectInfo: this.getObjectByLang('    ', 'Loading game information', ''),
      loadDataOfLatestOperations: this.getObjectByLang('    ', 'Loading data on recent transactions', ''),
      loadInventory: this.getObjectByLang(' ', 'Load inventory items', ''),
      chestItemLoad: this.getObjectByLang('    ', 'Load main information of chests', ''),
      loadHouseInfo: this.getObjectByLang('   ', 'Load home information', ''),
      loadPartnerInfo: this.getObjectByLang('    ', 'Load partner information', ''),
      loadItemsFromChest: this.getObjectByLang('   ', 'Loading items from chest', ''),
      loadUserItems: this.getObjectByLang('  ', 'Loading user items', ''),
      loadAllItems: this.getObjectByLang('  ', 'Loading game items', ''),
      loadAdventureInfo: this.getObjectByLang('    ', 'Loading adventure info', ''),
      loadTradeItems: this.getObjectByLang('   ', 'Load auction items', ''),
    }
    // this.lang.loadingComponentDictionary['key'][this.lang.getCurrentLang()]
  }


  changeLang(lang) {
    this.userLang = lang;
    localStorage.setItem('lang', lang);
  }

  getCurrentLang() {
    return this.userLang;
  }


  getAvailableLanguages() {
    return {
      'ru': {name: '', className: 'flag-ru', key: 'ru'},
      'en': {name: 'English', className: 'flag-us', key: 'en'},
      'china': {name: '', className: 'flag-china', key: 'china'}
    }
  }

  loadDictionary() {
    return new Promise(resolve => {
      if (Object.values(this.store.dictionary).length) {
        resolve();
        return false;
      }
      this.http.get('assets/dictionary.json').subscribe(response => {
        this.store.dictionary = response;
        resolve();
      });
    });
  }

  translate(key) {
    return this.store.dictionary[key] ? this.store.dictionary[key][this.langIdxByLang[this.userLang]] : '-----------';
  }

  getObjectByLang(ru, en, china) {
    return {
      ru,
      en,
      china
    }
  }
}
