  getLatestOperations() {
    return new Promise((resolve, reject) => {
      let tronWeb = this.trxWithoutWallet;
      tronWeb.getEventResult(this.addr, {limit: 50}).then((eventList: any) => {
        let events = {
          logPayments: [],
          logActionOfUser: [],
        };


        eventList.forEach(event => {
          let result = event.result;
          if (event.name === 'logPayments') {
            result.count = this.calcTrx(result.count, true);
          }

          result.when = parseInt(result.when) * 1000;

          events[event.name].push(result);
        });
        resolve(events);
      });
    });
  }

