  userPointsByDay(addr, day) {
    return new Promise((resolve, reject) => {
      Promise.all([this.contract.userPointsByDay(day, addr).call(), this.contract.totalDayPoints(day).call()]).then(([userPoints, allRatingData]) => {

        resolve({
          allRatingData,
          userPoints
        })
      }).catch(() => {
        this.userPointsByDay(addr, day).then(resolve)
      });
    });
  }


  getUserDividendsCount(addr = this.tronWeb.defaultAddress.hex) {
    return new Promise(async (resolve, reject) => {
      let today = await this.contract.calculateCurrentDayFromStart().call();
      let dateOfLatestGotDividends = await this.contract.latestGotDividends(addr).call();
      today = today.toNumber();
      dateOfLatestGotDividends = dateOfLatestGotDividends.toNumber();
      let promises = [];
      // debugger;


      for (let day = dateOfLatestGotDividends; day < today; day++) {
        promises.push(new Promise(async (resolve, reject) => {
          let dataOfRatingByUserAndDay: any = await this.userPointsByDay(addr, day);
          let pointsOfDay = dataOfRatingByUserAndDay.allRatingData.points.toNumber();

          let percentFromTop = pointsOfDay ? dataOfRatingByUserAndDay.userPoints / pointsOfDay * 100 : 0;
          let dividends = this.calcTrx(dataOfRatingByUserAndDay.allRatingData.poll * percentFromTop / 100, true);
          resolve(dividends);
        }));
      }

      Promise.all(promises).then((values: any) => {
        values = values.length === 0 ? [0] : values;
        resolve(eval(values.join('+')).toFixed(2));
      });
    });
  }

  getRatingData(day = undefined) {
    return new Promise(async (resolve, reject) => {
      let userAddr = this.tronWeb.defaultAddress.hex;
      let timeOfStartContract = await this.contract.dateOfStartSmartContract().call();
      timeOfStartContract *= 1000;
      day = day ? day : await this.contract.calculateCurrentDayFromStart().call();
      let totalOfDay = await this.contract.totalDayPoints(day).call();
      let totalUserCount = await this.contract.totalUserCount().call();


      let promisesOfUsers = [];

      //     
      for (let i = 0; i < totalUserCount - 1; i++) {
        promisesOfUsers.push(new Promise((resolve, reject) => {
          this.loadUserAddrById(i).then(async (addr) => {
            let userInfo: any = await this.userInfo(addr, false);
            this.contract.userPointsByDay(day, addr).call().then((points) => {
              resolve({
                addr,
                points: points.toNumber(),
                name: userInfo.name,
              })
            });
          })
        }));
      }

      Promise.all(promisesOfUsers).then(async (users) => {
        let totalDayPoints = totalOfDay.points.toNumber();
        let maxCountRating = 11;
        let userData = {};
        users = users.sort((user, userN) => {
          return userN.points - user.points;
        }).map((user, idx) => {
          let userPercent = totalDayPoints ? user.points / totalDayPoints * 100 : 0;
          user.place = idx + 1;
          user.percent = totalDayPoints ? userPercent.toFixed(2) + '%' : 0 + '%';
          if (user.addr === userAddr) {
            user.isUser = true;
            user.dividends = userPercent > 0 ? this.calcTrx(totalOfDay.poll * userPercent / 100, true).toFixed(2) : 0;

            userData = user;
          }
          return user;
        }).filter((user) => {
          return user.place <= maxCountRating || user.isUser;
        });


        resolve({
          user: userData,
          users,
          totalDayPoints,
          time: timeOfStartContract,
          poll: this.calcTrx(totalOfDay.poll, true)
        });
      });


      // this.contract.latestGotDividends().then()
    });
  }


