  loadUsers(from, count = 0) {
    return new Promise((resolve, reject) => {
      this.isContractExist(async () => {
        let promises = [];
        let lengthUsers = await this.contract.totalUserCount().call();
        for (let i = 0; i < lengthUsers - 1; i++) {
          if (i >= from && i <= from + count) {
            promises.push(new Promise(res => {
              this.contract.users(i).call().then(addr => {
                let promises = {
                  house: new Promise(resolver => this.getUserHouse(addr).then(resolver)),
                  info: new Promise(resolver => this.userInfo(addr).then(resolver))
                };
                let promiseValues = Object.values(promises);

                Promise.all(promiseValues).then(([house, info]) => {
                  res({house, info, addr})
                });

              });
            }));
          }
        }

        Promise.all(promises).then(users => {
          resolve({lengthUsers: lengthUsers, users})
        });
      });
    });
  }


