  userInfo(addr = null, loadResources = true, loadPoints = false) {
    return new Promise((resolve, reject) => {
      this.isContractExist(() => {
        let promises = {resources: null, player: null, points: null};
        addr = addr || this['tronWeb'].defaultAddress.base58;
        promises.player = new Promise(res => this.contract.Players(addr).call().then(res));
        loadResources && (promises.resources = new Promise(res => this.contract.PlayersResources(addr).call().then(res)));

        loadPoints && (() => {
          promises.points = new Promise(resolve => {
            this.contract.calculateCurrentDayFromStart().call().then((day) => {
              this.contract.userPointsByDay(day, addr).call().then(res => {
                resolve(res);
              });
            });
          })
        })();

        Promise.all(Object.values(promises)).then(([resources, player, points]) => {
          player = this.bs.calculateItem(player);
          player.regTime = player.regTime * 1000;
          player.bonusTime = player.bonusTime * 1000;
          player.earn = this.calcTrx(player.earn, true);
          player.points = points;
          if (resources && player) {
            resolve(Object.assign(this.bs.calculateItem(resources), player));
          } else {
            resolve(player);
          }
        });

      })
    });
  }

