  loadAvailableHouses() {
    return new Promise((resolve) => {
      this.trxWithoutWallet.contract().at(this.addr).then(contract => {
        contract.getHouseLength().call().then(length => {
          let promisesArr = [];
          for (let i = 0; i < length; i++) {
            promisesArr.push(new Promise(resolve => {
              this.houseInfo(i).then((res) => {
                resolve(res);
              });
            }));
          }

          Promise.all(promisesArr).then(values => {
            let houses = values.map(item => {
              item = this.bs.calculateItem(item);
              item.price = this.calcTrx(item.price, true);
              return item;
            });

            houses = houses.filter(home => home.exist);
            resolve(houses);
          });
        });

      })
    });
  }

