  getUserHouse(addr = null) {
    return new Promise((resolve, reject) => {
      this.isContractExist(async () => {
        addr = addr ? addr : this['tronWeb'].defaultAddress.base58;
        this.contract.Houses(addr).call().then(res => {
          (async () => {
            let levelBonus = {wood: 0, stone: 0, iron: 0, gold: 0, diamond: 0, food: 0};
            for (let i = 0; i < res.level; i++) {
              let levelHouseInfo: any = await this.getHouseLevelInfo(i);
              levelBonus.wood += levelHouseInfo.increaseWood.toNumber();
              levelBonus.stone += levelHouseInfo.increaseStone.toNumber();
              levelBonus.iron += levelHouseInfo.increaseIron.toNumber();
              levelBonus.gold += levelHouseInfo.increaseGold.toNumber();
              levelBonus.food += levelHouseInfo.increaseFood.toNumber();
            }
            this.houseInfo(res.idx).then(result => {
              if (!res || !result) {
                reject();
                return false;
              }


              let data = Object.assign(res, result);
              data = {
                idx: data.idx.toNumber(),
                level: data.level,
                income: [
                  {value: data.food.toNumber(), name: 'Food', bonus: levelBonus.food},
                  {value: data.wood.toNumber(), name: 'Wood', bonus: levelBonus.wood},
                  {value: data.stone.toNumber(), name: 'Stone', bonus: levelBonus.stone},
                  {value: data.iron.toNumber(), name: 'Iron', bonus: levelBonus.iron},
                  {value: data.gold.toNumber(), name: 'Gold', bonus: levelBonus.gold},
                  {value: data.diamond.toNumber(), name: 'Diamond', bonus: levelBonus.diamond}
                ],
              };
              resolve(data);
            });
          })();
        })
      })
    });
  }


