  getTradeItemsId(pageSize = 150) {
    let getItemInfoByUserAndItemId = (owner, itemId) => {
      return new Promise((resolve, reject) => {
        this.itemUserList(owner, itemId).then((res) => {
          let key = this.itemService.getItemKey(res);
          resolve({
            key,
            info: res,
            name: this.itemService.nameAndBkgPosOfItem[key].name
          });
        }).catch((err) => {
          this.service.errorHandle(err);
          reject();
        });

      });
    };

    return new Promise(async (resolve, reject) => {
      let countOfItems = await this.contract.getTradeLength().call();
      let userTradeIds: any = await this.getUserTradeItems(); // id of user trade items


      let maxItems = 10;

      let itemsId = [];

      for (let i = Math.ceil(countOfItems / maxItems); i >= 0; i--) {
        if (itemsId.length >= pageSize) break;
        let itemsList: any = await this.contract.getAvailableTradeItems(maxItems * i).call();
        itemsList = this.bs.calculateItem(this.bs.splitIdsAndReturnArray(itemsList));
        itemsId.push(...itemsList);
      }

      itemsId.push(...userTradeIds);
      itemsId = uniq(itemsId);
      let promiseOfItemTrade = [];
      itemsId.forEach(id => {
        promiseOfItemTrade.push(new Promise((resolve, reject) => {
          this.contract.listItemTrade(id).call().then((res) => {
            let tradeItem = this.bs.calculateItem(res);
            tradeItem.price = this.calcTrx(tradeItem.price, true);
            tradeItem.time = tradeItem.time * 1000;
            resolve(tradeItem);
          }).catch((err) => {
            this.service.errorHandle(err);
            reject();
          });
        }));
      });

      Promise.all(promiseOfItemTrade).then(values => {
        let promisesOfItemUsers = [];
        values.forEach(trade => {
          promisesOfItemUsers.push(
            new Promise((resolve, reject) => {
              getItemInfoByUserAndItemId(trade.owner, trade.itemUserId).then((result: any) => {
                trade.info = result.info;
                trade.info.name = result.name;
                resolve();
              });
            })
          );
        });


        Promise.all(promisesOfItemUsers).then(async () => {
          values = values.sort((item, nItem) => nItem.time - item.time);
          resolve(values);
        });
      });
    });

  }


