  // ItemsLogic
  loadUserItems(addr = undefined, useType = undefined, from = 0, pageSize = 135) {
    return new Promise((resolve, reject) => {
      this.isContractExist(async () => {
        addr = addr || this['tronWeb'].defaultAddress.base58;
        let promises = [];
        let userItems: any = [];
        let countOfUserItems: any = await this.contract.itemsUserLength(addr).call();
        countOfUserItems = parseInt(countOfUserItems.toNumber());
        if (useType === undefined) {
          let countOfItemsKeys = Array(countOfUserItems).keys();
          // @ts-ignore
          userItems = [...countOfItemsKeys];
        } else {
          let listIds: any = null;
          if (useType !== 2) { //      
            //  10     1 .
            //      
            let maxCountGetItems = 10;
            for (let i = 0; i < Math.ceil(countOfUserItems / maxCountGetItems); i++) {
              if (userItems.length >= pageSize || countOfUserItems <= maxCountGetItems * i) break;
              let userItem: any = await this.getUserItemsIdByUseType(addr, useType, maxCountGetItems * i); // 0, 10, 20
              userItem = this.bs.splitIdsAndReturnArray(userItem);
              userItems.push(...userItem);
            }
          } else {
            listIds = await this.contract.dressedUserItems(addr).call();
            userItems = this.bs.calculateItem(Object.values(listIds)).filter(item => item >= 0);
          }
        }

        userItems = uniq(userItems);
        if (userItems.length > pageSize) userItems = userItems.slice(0, -(userItems.length - pageSize));
        for (let i = 0; i < userItems.length; i++) {
          promises.push(new Promise((resolve, reject) => {
              (async () => {
                let item: any = await this.itemUserList(addr, userItems[i]);
                resolve(item);
              })();
            })
          )
        }

        Promise.all(promises).then(values => {
          if (useType !== 3) {
            values = values.filter(valItem => {
              return valItem.itemParams.available && valItem.available;
            });
          }
          resolve(values);
        });
      });
    });
  }


