  getDeletedMessagesByModer(addr) {
    return new Promise((resolve, reject) => {
      this.isContractExist(async () => {
        let promises = [];
        let lengthOfDeletedMessages: any = await this.contract.getDeletedMessagesLength(addr).call();
        for (let i = 0; i < lengthOfDeletedMessages; i++) {
          promises.push(new Promise(res => {
            this.contract.getDeletedMessagesById(addr, i).call().then(res);
          }));
        }

        Promise.all(promises).then(messages => {
          messages = messages.map(msg => {
            return {message: msg[0], time: msg[1].toNumber() * 1000}
          });
          resolve(messages);
        });
      });
    });
  }

  /**
   *
   * @param count
   * @param type -1 ,  (0 - , 1 - , 2 - )
   */
  getMessagesList(count, type) {
    let loadMessageByType = (type, length, promises) => {
      for (let i = length - 1; i >= 0; i--) {
        if (length - count <= i) {
          if (length - count <= i) promises.push(this.getMessageByIdx(i, type));
        }
      }
    };


    let loadMessageLength = (key, resolver) => {
      let timer = setTimeout(() => {
        loadMessageLength(key, resolver);
      }, 1000);


      this.contract.getMessageLength(key).call().then(res => {
        clearTimeout(timer);
        resolver(res);
      });
    };


    return new Promise((resolve) => {
      this.isContractExist(async () => {
        let messagePromises = [];
        let messagesLengthByType = {  //   (0 - , 1 - , 2 - )
          0: null,
          1: null,
          2: null
        };

        let messagesLengthKeys = Object.keys(messagesLengthByType);
        let messageLengthPromise = [];

        for (let i = 0; i < messagesLengthKeys.length; i++) {
          messageLengthPromise.push(new Promise(resolve => {
            loadMessageLength(messagesLengthKeys[i], resolve);
          }));
        }


        Promise.all(messageLengthPromise).then(valuesOfLength => {
          if (type === -1) {
            // All, load everything!

            each(valuesOfLength, (value, key) => {
              loadMessageByType(key, value.toNumber(), messagePromises);
            });

          } else {
            loadMessageByType(type, valuesOfLength[type].toNumber(), messagePromises);
          }


          Promise.all(messagePromises).then(values => {
            let sortedValues = sortBy(values, ['time']);
            let slicesMessages = sortedValues.slice(-count);
            let userInfoPromises = [];


            for (let i = 0; i < slicesMessages.length; i++) {
              userInfoPromises.push(this.userInfo(slicesMessages[i].sender, false));
            }


            Promise.all(userInfoPromises).then(values => {

              resolve(slicesMessages.map((msg, idx) => {
                msg.senderName = values[idx].name;
                msg = this.bs.calculateItem(msg);
                return msg;
              }));
            });

          });

        });


      });
    });
  }


