  getBannedUserHistory(userAddr) {
    return new Promise((resolve, reject) => {
      //    ,      
      (async () => {
        let promisesOfLength = [];
        let moderList: any = await this.getModerList();
        moderList.forEach(moderAddr => {
          promisesOfLength.push(new Promise((res) => {
            this.contract.getBannedUserLengthByModerAddr(userAddr, moderAddr).call().then(res);
          }));
        });

        Promise.all(promisesOfLength).then(lengths => {
          let promises = [];
          lengths.forEach((length, idx) => {
            let moderAddr = moderList[idx];
            promises.push(new Promise((res) => {
              this.contract.getBannedUserReason(userAddr, moderAddr, idx).call().then(res);
            }));


          });

          Promise.all(promises).then(values => {
            resolve(values)
          });
        });
      })();
    });
  }


