  uploadChestTemplate(template) {
    return new Promise((resolve, reject) => {
      let chance = this.bs.zipArray(template.chance);
      let price = template.price === -1 ? -1 : this.calcTrx(template.price);

      this.contract.uploadChestTemplate(chance, price).send(this.getOptionsWithPrice(0, true)).then((res) => {
        // ,   loot
        let promises = [];
        template.itemList.forEach((item, index) => {
          promises.push(new Promise((resolver, reject) => {
            let resources = this.bs.zipArray(item.resources);
            this.contract.updateChestTemplateLoot(res.toNumber(), [item.percentFrom, item.percentTo, item.itemId, resources[0], resources[1], resources[2], resources[3], resources[4], resources[5]]).send(this.getOptionsWithPrice(0)).then(res => {
              resolver({lootId: index, type: 'success'});
            }).catch((err) => {
              resolver({lootId: index, type: 'fail'});
              this.service.errorHandle(err);
            });
          }));
        });

        Promise.all(promises).then(values => {
          resolve(values);
        });


      }).catch((err) => {
        this.service.errorHandle(err);
        reject();
      });
    });
  }

  updateChestTemplate(id, params, price) {
    return new Promise((resolve, reject) => {
      price = price === -1 ? -1 : this.calcTrx(price);

      this.contract.updateChestTemplate(id, params, price).send(this.getOptionsWithPrice(0)).then((res) => {
        resolve(res);
      }).catch((err) => {
        this.service.errorHandle(err);
        reject();
      });
    });
  }

