  getHistoryOfAdventure(addr = null, maxSize = 50) {
    return new Promise((resolve, reject) => {
      this.isContractExist(async () => {
        addr = addr || this['tronWeb'].defaultAddress.base58;
        let promises = [];
        let lengthOfHistoryAdventure: any = await this.getAdventureLength(addr);
        for (let i = lengthOfHistoryAdventure - 1; i >= 0; i--) {
          if (promises.length >= maxSize) break;
          promises.push(
            new Promise(resolver => {
              this.getAdventureHistoryById(addr, i).then(resolver);
            })
          )
        }

        Promise.all(promises).then(values => {
          resolve(values.sort((i, n) => n.time - i.time));
        });
      });
    });
  }


  getUserTradeItems(addr = this['tronWeb'].defaultAddress.base58) {
    return new Promise(async (resolve, reject) => {
      let userItemLength: any = await this.contract.getUserTradeLength(addr).call();
      let promises = [];
      for (let i = 0; i < userItemLength; i++) {
        promises.push(new Promise((resolve, reject) => {
          this.contract.userItemsInTrade(addr, i).call().then((tradeItem) => {
            tradeItem.available && resolve(tradeItem.tradeId);
            !tradeItem.available && resolve(-1);
          })
        }));
      }

      Promise.all(promises).then(tradeIds => {
        tradeIds = tradeIds.filter(id => id >= 0);
        resolve(this.bs.calculateItem(tradeIds));
      });
    });
  }


