  loadAllUserChests(addr = undefined, isOpened = false, pageSize = 135) {
    return new Promise((resolve, reject) => {
      this.isContractExist(async () => {
        addr = addr || this['tronWeb'].defaultAddress.base58;
        let countOfChest = await this.contract.getUserChestLength(addr).call();
        let maxChest = 10;
        let chestIds = [];


        for (let i = 0; i < Math.ceil(countOfChest / maxChest); i++) {
          if (chestIds.length >= pageSize || countOfChest <= maxChest * i) break;
          let chestList: any = await this.getAvailableUserChest(addr, isOpened, maxChest * i);
          chestList = this.bs.splitIdsAndReturnArray(chestList);
          chestIds.push(...chestList);
        }

        let promises = [];
        chestIds.forEach(id => {
          promises.push(new Promise(resolver => {
            this.getUserChestById(addr, id).then(resolver);
          }));
        });
        Promise.all(promises).then(resolve);
      });
    });
  }

