  loadLootChestById(id, lootId) {
    return new Promise((resolve, reject) => {
      this.contract.getLootOfChest(id, lootId).call().then((res) => {
        /**
         *  ,      :
         *
         loot.percentFrom,
         loot.percentTo,
         loot.itemId,
         loot.food,
         loot.wood,
         loot.stone,
         loot.iron,
         loot.gold,
         loot.diamond
         *
         */
        let paramsOfChest = this.bs.calculateItem(res);

        resolve({
          lootId,
          percentFrom: paramsOfChest[0],
          percentTo: paramsOfChest[1],
          itemId: paramsOfChest[2],
          food: paramsOfChest[3],
          wood: paramsOfChest[4],
          stone: paramsOfChest[5],
          iron: paramsOfChest[6],
          gold: paramsOfChest[7],
          diamond: paramsOfChest[8]
        });
      }).catch((err) => {
        this.loadLootChestById(id, lootId).then(resolve)
      });
    });

  }

  loadChestById(id) {
    return new Promise((resolve, reject) => {
      this.isContractExist(async () => {
        let lootLength = await this.contract.getLootLengthOfChest(id).call();
        let chestInfo = await this.contract.chestList(id).call();
        let promises = [];

        for (let lootId = 0; lootId < lootLength; lootId++) {
          promises.push(new Promise((resolver) => {
            this.loadLootChestById(id, lootId).then(resolver);
          }));
        }

        Promise.all(promises).then(loot => {
          chestInfo = this.bs.calculateItem(chestInfo);
          chestInfo.price = chestInfo.price === -1 ? -1 : this.calcTrx(chestInfo.price, true);
          resolve(Object.assign(chestInfo, {
            loot: loot.sort((a, b) => {
              return a.percentFrom - b.percentFrom;
            })
          }));
        });
      });
    });
  }

