import {Component, Injectable, InjectionToken, Injector, Input, OnInit, ReflectiveInjector} from '@angular/core';
import {NgbActiveModal, NgbModal} from "@ng-bootstrap/ng-bootstrap";
import {UserInfoComponent} from "../components/modals/user-info/user-info.component";
import {TronlinkComponent} from "../components/modals/tronlink/tronlink.component";
import {AlertComponentComponent} from "../game/components/alerts/alert-component/alert-component.component";
import {CraftingTableComponent} from "../game/components/crafting-table/crafting-table.component";
import {TITLE} from "./base.service";

@Component({
  selector: 'modal-template',
  template: `
      <div class="modal-header" *ngIf="params.hasTitle">
          <h4 class="modal-title" [innerText]="params.title"></h4>
          <button type="button" class="close" aria-label="Close" (click)="close()">
              <span aria-hidden="true">&times;</span>
          </button>
      </div>
      <div class="modal-body">
          <ng-content *ngComponentOutlet="childComponent;injector: dynamicComponentInjector"></ng-content>
      </div>
      <div class="modal-footer" *ngIf="params.hasCloseBtn">
          <button type="button" class="btn btn-outline-dark" (click)="close()"></button>
      </div>
  `
})
export class ModalTemplate implements OnInit {
  @Input() title;
  @Input() childComponent;
  @Input() params;
  @Input() data;
  dynamicComponentInjector: Injector;

  constructor(public activeModal: NgbActiveModal, private parentInjector: Injector) {

  }

  ngOnInit() {
    this.dynamicComponentInjector = Injector.create([{
      provide: TITLE,
      useValue: this.data
    }], this.parentInjector);
  }

  close() {
    this.activeModal.dismiss('close');
  }
}

export interface ModalServiceOptions {
  data: any;
  title: string
  componentName: string,
  windowClass?: string,
  hasCloseBtn?: boolean
  hasTitle?: boolean
  size?: string
}


@Injectable({
  providedIn: 'root'
})
export class ModalService {

  constructor(private mService: NgbModal) {
  }

  /**
   * title
   * template
   * @param options
   */
  open(options: ModalServiceOptions) {
    let types = {
      'moder-info': UserInfoComponent,
      'app-tronlink': TronlinkComponent,
      'app-alert-component': AlertComponentComponent
    };

    let modal = this.mService.open(ModalTemplate, {
    // @ts-ignore
      size: options.size || 'lg',
      windowClass: options.windowClass || '',
      centered: true
    });

    // modal.contentRef = types[options.componentName];

    modal.componentInstance.params = {
      title: options.title,
      hasCloseBtn: typeof options.hasCloseBtn === 'boolean' ? options.hasCloseBtn : true,
      hasTitle: typeof options.hasTitle === 'boolean' ? options.hasTitle : true,
    };


    modal.componentInstance.data = Object.assign({data: options.data}, {modal});
    modal.componentInstance.childComponent = types[options.componentName];
    return modal;
  }

  moreInfoAboutUser(addr) {
    this.open({
      title: "   ",
      data: addr,
      componentName: 'moder-info'
    });
  }

  openMineCraftAlert(params) {
    return this.open({
      title: "",
      data: params.data,
      componentName: 'app-alert-component',
      windowClass: 'game-modal-alert',
      hasCloseBtn: false,
      hasTitle: false,
      size: 'xs' // xs is not exist but i cant send empty size.
    });
  }
}
