import {Injectable} from '@angular/core';
import {BaseService} from "../base.service";
import {uniqBy, map} from 'lodash';
import {LangService} from "../../game/services/lang.service";

@Injectable({
  providedIn: 'root'
})
export class ItemService {
  resourcesName = null;
  keysAndNamesOfItem = null;
  nameAndBkgPosOfItem = null;
  chestPath = "assets/game/base/interface/";


  constructor(private bs: BaseService) {
    let sizeOfItem = 64;
    this.nameAndBkgPosOfItem = { // key is typeId, MadeTypeId, armourTypeId, toolTypeId  of item => typeId|MadeTypeId|armorTypeId|toolTypeId
      // list of swords
      '0|0|-1|-1': {backgroundPos: `${-sizeOfItem * 5}px ${-sizeOfItem * 3}px`, key: 'woodenSword'},
      '0|1|-1|-1': {backgroundPos: `${-sizeOfItem * 6}px ${-sizeOfItem * 3}px`, key: 'stoneSword'},
      '0|2|-1|-1': {backgroundPos: `${-sizeOfItem * 7}px ${-sizeOfItem * 3}px`, key: 'ironSword'},
      '0|3|-1|-1': {backgroundPos: `${-sizeOfItem * 8}px ${-sizeOfItem * 3}px`, key: 'goldenSword'},
      '0|4|-1|-1': {backgroundPos: `${-sizeOfItem * 9}px ${-sizeOfItem * 3}px`, key: 'diamondSword'},

      // list of armour
      // HEAD
      '1|0|0|-1': {backgroundPos: `${0}px ${-sizeOfItem}px`, key: 'woodenHelmet'},
      '1|1|0|-1': {backgroundPos: `${-sizeOfItem}px ${-sizeOfItem}px`, key: 'stoneHelmet'},
      '1|2|0|-1': {backgroundPos: `${-sizeOfItem * 2}px ${-sizeOfItem}px`, key: 'ironHelmet'},
      '1|3|0|-1': {backgroundPos: `${-sizeOfItem * 3}px ${-sizeOfItem}px`, key: 'goldenHelmet'},
      '1|4|0|-1': {backgroundPos: `${-sizeOfItem * 4}px ${-sizeOfItem}px`, key: 'diamondHelmet'},

      // Armour
      '1|0|1|-1': {backgroundPos: `${0}px ${-sizeOfItem * 2}px`, key: 'woodenArmour'},
      '1|1|1|-1': {backgroundPos: `${-sizeOfItem}px ${-sizeOfItem * 2}px`, key: 'stoneArmour'},
      '1|2|1|-1': {backgroundPos: `${-sizeOfItem * 2}px ${-sizeOfItem * 2}px`, key: 'ironArmour'},
      '1|3|1|-1': {backgroundPos: `${-sizeOfItem * 3}px ${-sizeOfItem * 2}px`, key: 'goldenArmour'},
      '1|4|1|-1': {backgroundPos: `${-sizeOfItem * 4}px ${-sizeOfItem * 2}px`, key: 'diamondArmour'},

      // LEGS
      '1|0|2|-1': {backgroundPos: `${0}px ${-sizeOfItem * 3}px`, key: 'woodenLeg'},
      '1|1|2|-1': {backgroundPos: `${-sizeOfItem}px ${-sizeOfItem * 3}px`, key: 'stoneLeg'},
      '1|2|2|-1': {backgroundPos: `${-sizeOfItem * 2}px ${-sizeOfItem * 3}px`, key: 'ironLeg'},
      '1|3|2|-1': {backgroundPos: `${-sizeOfItem * 3}px ${-sizeOfItem * 3}px`, key: 'goldenLeg'},
      '1|4|2|-1': {backgroundPos: `${-sizeOfItem * 4}px ${-sizeOfItem * 3}px`, key: 'diamondLeg'},

      // Boots
      '1|0|3|-1': {backgroundPos: `${0}px ${-sizeOfItem * 4}px`, key: 'woodenBoots'},
      '1|1|3|-1': {backgroundPos: `${-sizeOfItem}px ${-sizeOfItem * 4}px`, key: 'stoneBoots'},
      '1|2|3|-1': {backgroundPos: `${-sizeOfItem * 2}px ${-sizeOfItem * 4}px`, key: 'ironBoots'},
      '1|3|3|-1': {backgroundPos: `${-sizeOfItem * 3}px ${-sizeOfItem * 4}px`, key: 'goldenBoots'},
      '1|4|3|-1': {backgroundPos: `${-sizeOfItem * 4}px ${-sizeOfItem * 4}px`, key: 'diamondBoots'},

      // TOOLS
      // PICKAXE
      '2|0|-1|0': {backgroundPos: `${-sizeOfItem * 5}px ${-sizeOfItem * 2}px`, key: 'woodenPickaxe'},
      '2|1|-1|0': {backgroundPos: `${-sizeOfItem * 6}px ${-sizeOfItem * 2}px`, key: 'stonePickaxe'},
      '2|2|-1|0': {backgroundPos: `${-sizeOfItem * 7}px ${-sizeOfItem * 2}px`, key: 'ironPickaxe'},
      '2|3|-1|0': {backgroundPos: `${-sizeOfItem * 8}px ${-sizeOfItem * 2}px`, key: 'goldenPickaxe'},
      '2|4|-1|0': {backgroundPos: `${-sizeOfItem * 9}px ${-sizeOfItem * 2}px`, key: 'diamondPickaxe'},

      // HOE
      '2|0|-1|1': {backgroundPos: `${-sizeOfItem * 5}px ${-sizeOfItem}px`, key: 'woodenHoe'},
      '2|1|-1|1': {backgroundPos: `${-sizeOfItem * 6}px ${-sizeOfItem}px`, key: 'stoneHoe'},
      '2|2|-1|1': {backgroundPos: `${-sizeOfItem * 7}px ${-sizeOfItem}px`, key: 'ironHoe'},
      '2|3|-1|1': {backgroundPos: `${-sizeOfItem * 8}px ${-sizeOfItem}px`, key: 'goldenHoe'},
      '2|4|-1|1': {backgroundPos: `${-sizeOfItem * 9}px ${-sizeOfItem}px`, key: 'diamondHoe'},


      // Resources
      'resources': {
        'food': {backgroundPos: `0 0`, key: 'food'},
        'wood': {backgroundPos: `${-sizeOfItem}px 0`, key: 'wood'},
        'stone': {backgroundPos: `${-sizeOfItem * 2}px 0`, key: 'stone'},
        'iron': {backgroundPos: `${-sizeOfItem * 3}px 0`, key: 'iron'},
        'gold': {backgroundPos: `${-sizeOfItem * 4}px 0`, key: 'gold'},
        'diamond': {backgroundPos: `${-sizeOfItem * 5}px 0`, key: 'diamond'}
      },

      'chest': {
        'food': {
          key:'eatChest',
          backgroundPos: `${-sizeOfItem * 5}px ${-sizeOfItem * 4}px`,
          img: this.chestPath + 'chest1.png',
        },
        'rare': {
          key:'rareChest',
          backgroundPos: `${-sizeOfItem * 6}px ${-sizeOfItem * 4}px`,
          img: this.chestPath + 'chest2.png',
        },
        'epic': {
          key:'epicChest',
          backgroundPos: `${-sizeOfItem * 7}px ${-sizeOfItem * 4}px`,
          img: this.chestPath + 'chest3.png',
        }

      }
    };


    this.keysAndNamesOfItem = [
      {key: 'Damage'}, // key should be with uppercase! It's need for charDamage, charLoot and etc
      {key: 'Loot'},
      {key: 'Defence'},
      {key: 'Durability'}
    ]


  }

  getEmptyItem(template = null) {
    let name = template ? template.name : {RU: "", EN: ""};
    let id = template ? template.id : null;
    let data = {
      name, //    ,   
      id,
      params: [
        {
          type: 0, // []
          name: "",
          madeTypesInclude: true, //    
          madeTypesId: 0,
          armorTypeInclude: false,
          armorTypeId: -1,
          toolTypeId: -1,
          craft: this.bs.getItemsCraftParamLootCharArrByKey('craft'), // count of [wood, stone, iron, gold, diamond]
          loot: this.bs.getItemsCraftParamLootCharArrByKey('loot'), // chance of [end, mine, hell, farm]
          char: this.bs.getItemsCraftParamLootCharArrByKey('char'),
          params: this.bs.getItemsCraftParamLootCharArrByKey('params'),
          available: false,
        },
        {
          type: 1, // []
          name: "",
          madeTypesInclude: true, //    
          armorTypeInclude: true,
          madeTypesId: 0,
          armorTypeId: 0, //
          toolTypeId: -1,
          craft: this.bs.getItemsCraftParamLootCharArrByKey('craft'), // count of [wood, stone, iron, gold, diamond]
          loot: this.bs.getItemsCraftParamLootCharArrByKey('loot'), // chance of [end, mine, hell, farm]
          char: this.bs.getItemsCraftParamLootCharArrByKey('char'),
          params: this.bs.getItemsCraftParamLootCharArrByKey('params'),
          available: false,
        },
        {
          type: 2, // []
          name: "",
          madeTypesInclude: true, //    
          typeInclude: false,
          armorTypeId: -1,
          madeTypesId: 0,
          toolTypeId: 0,
          toolTypeInclude: true,
          craft: this.bs.getItemsCraftParamLootCharArrByKey('craft'), // count of [wood, stone, iron, gold, diamond]
          loot: this.bs.getItemsCraftParamLootCharArrByKey('loot'), // chance of [end, mine, hell, farm]
          char: this.bs.getItemsCraftParamLootCharArrByKey('char'),
          params: this.bs.getItemsCraftParamLootCharArrByKey('params'),
          available: false,
        }, {
          type: 3, // []
          name: "",
          madeTypesInclude: false, //    
          typeInclude: false,
          madeTypesId: -1,
          armorTypeId: -1,
          toolTypeId: -1,
          craft: this.bs.getItemsCraftParamLootCharArrByKey('craft'), // count of [wood, stone, iron, gold, diamond]
          loot: this.bs.getItemsCraftParamLootCharArrByKey('loot'), // chance of [end, mine, hell, farm]
          char: this.bs.getItemsCraftParamLootCharArrByKey('char'),
          params: this.bs.getItemsCraftParamLootCharArrByKey('params'),
          available: false,
        }
      ]
    };


    data.params = map(data.params, par => {
      if (template && par.type === template.type) {
        par.available = true;
        par.char = uniqBy([...template.char, ...par.char], 'id');
        par.craft = uniqBy([...template.craft, ...par.craft], 'id');
        par.loot = uniqBy([...template.loot, ...par.loot], 'id');
        par.params = uniqBy([...template.params, ...par.params], 'id');
        par.madeTypesId = template.madeTypesId;
        par.armorTypeId = template.armorTypeId;
        par.toolTypeId = template.toolTypeId;
      }

      return par;
    });


    return data;
  }

  getItemTypeUseById(key) {
    // // [0 -> InBackPack, 1 -> InChest, 2 -> isDressed, 3 -> InAuction]
    let types = {
      0: 'backpack',
      1: 'chest',
      2: 'dressed',
      3: 'auction',
      'backpack': 0,
      'chest': 1,
      'dressed': 2,
      'auction': 3
    };
    return types[key];

  }

  handlerEmptyDataItem(item, id) {
    item = this.bs.calculateItem(item);
    item = {
      id,
      craft: this.bs.calculateItem(item[0]),
      loot: this.bs.calculateItem(item[1]),
      char: this.bs.calculateItem(item[2]),
      params: this.bs.calculateItem(item[3]),
      type: this.bs.calculateItem(item['typeId']),
      madeTypesId: this.bs.calculateItem(item['madeTypesId']),
      armorTypeId: this.bs.calculateItem(item['armorTypeId']),
      toolTypeId: this.bs.calculateItem(item['toolTypeId']),
      available: this.bs.calculateItem(item['available']),
    };

    item = this.bs.itemUnzip(item);


    return item;
  }

  getParamsOfCreate() {
    return [
      {
        title: ' ',
        key: 'params',
        description: '    , , ,    .'
      },
      {
        title: ' ',
        key: 'char',
        description: '     .'
      },
      {
        title: '',
        key: 'craft',
        description: '       ,     id   ,  9   .'
      },
      {
        title: ' ',
        key: 'loot',
        description: '      0 - 100%.'
      }
    ];

  }

  getSelectableItems() {
    return {
      madeTypes: [
        {
          key: 'wood',
          name: ""
        },
        {
          key: 'stone',
          name: ""
        },
        {
          key: 'iron',
          name: ""
        },
        {
          key: 'gold',
          name: ""
        },
        {
          key: 'diamond',
          name: ""
        }
      ],
      armorType: [
        {
          id: this.bs.getArmorTypeById('head'),
          key: this.bs.getArmorTypeById(0, true),
          name: ""
        },
        {
          id: this.bs.getArmorTypeById('armour'),
          key: this.bs.getArmorTypeById(1, true),
          name: ""
        },
        {
          id: this.bs.getArmorTypeById('leggings'),
          key: this.bs.getArmorTypeById(2, true),
          name: ""
        },
        {
          id: this.bs.getArmorTypeById('legs'),
          key: this.bs.getArmorTypeById(3, true),
          name: ""
        }
      ],
      toolType: [
        {
          id: this.bs.getToolTypeById('pickaxe'),
          key: this.bs.getToolTypeById(0, true),
          name: ''
        },
        {
          id: this.bs.getToolTypeById('hoe'),
          key: this.bs.getToolTypeById(1, true),
          name: ''
        }
      ]
    }
  }

  getItemKey(item: any) {
    return item.type + '|' + item.madeTypesId + '|' + item.armorTypeId + '|' + item.toolTypeId
  }
}
