import {Component} from '@angular/core';
import {ActivatedRoute, Route, Router} from "@angular/router";
import {StoreService} from "./game/services/store/store.service";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.sass']
})
export class AppComponent {
  title = 'front-app';

  constructor(private activatedRoute: ActivatedRoute, private store: StoreService, private router: Router) {
    this.activatedRoute.queryParams.subscribe(params => {
      let refUser = params['ref'];
      let userFrom = params['userFrom'];
      if (refUser) {
        localStorage.setItem(this.store.nameOfLocalStorageReferralKey, refUser);
      }

      if (userFrom) {
        localStorage.setItem(this.store.nameOfLocalStorageUserFromKey, userFrom);
      }

      if (refUser || userFrom) window.location.href = this.router.url.split('?')[0];
    });
  }


}
