import {Component, OnDestroy, OnInit} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {LoadingService} from "../../services/loading/loading.service";
import {StoreService} from "../../services/store/store.service";
import {BaseService} from "../../../services/base.service";
import {ItemService} from "../../../services/items/item.service";
import {LocationService} from "../../services/location.service";
import {LangService} from "../../services/lang.service";

@Component({
  selector: 'game-inventory',
  templateUrl: './inventory.component.html',
  styleUrls: ['./inventory.component.sass']
})
export class InventoryComponent implements OnInit, OnDestroy {
  items: any;
  chestItems: any = null;
  params: any = [];
  dressedItemsKeys = [];
  isLoadInventory = false;
  elementCountPerPage = 27;
  userAddr: any;
  typeLinks: any = [];
  updateItems: any = false;


  constructor(private cs: ContractService, private ls: LoadingService, public store: StoreService, private bs: BaseService, private itemService: ItemService, public locationService: LocationService, public lang: LangService) {
    this.userAddr = cs.tronWeb.defaultAddress.base58;
    // this.changePage = this.changePage.bind(this);

    this.typeLinks = [
      {
        id: 0,
        active: false,
        backgroundPos: this.itemService.nameAndBkgPosOfItem['1|4|1|-1'].backgroundPos
      },
      {
        id: 1,
        active: false,
        backgroundPos: this.itemService.nameAndBkgPosOfItem.chest['epic'].backgroundPos
      }
    ];


  }

  ngOnInit() {
    this.dressedItemsKeys = Object.keys(this.store.dressedItems);

    this.params = [
      {name: () => this.lang.translate('damage'), value: this.findSumByKeys('sword', 'damage')},
      {name: () => this.lang.translate('defence'), value: this.findSumByKeys('armour', 'defence')},
      {name: () => this.lang.translate('loot'), value: this.findSumByKeys('tools', 'loot')},
      {name: () => this.lang.translate('levelHouse'), value: this.store.loadedData.homeLoad.level}
    ];

    this.changeInventoryType(0, true);

    this.dressedItemsKeys.forEach(key => {
      console.log('this.store.dressedItems[key]:', this.store.dressedItems[key]);
    })
  }

  ngOnDestroy() {
    for (let member in this) this[member] = null
  }

  findSumByKeys(typeOfItems, type) {
    let keys = {
      tools: ['hoe', 'pickaxe'],
      armour: ['head', 'armour', 'legs', 'boots'],
      sword: ['sword'],
    };

    function capitalizeFirstLetter(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    }

    return (eval(keys[typeOfItems].map(key => {
      if (!this.store.dressedItems[key]) return 0;
      return this.store.dressedItems[key].params[this.bs.getPropertyIdByKey(type)].value + (this.store.dressedItems[key].itemParams['char' + capitalizeFirstLetter(type)] * 2);
    }).join('+')));
  }


  changeInventoryType(typeId, isFirst = false) {
    this.typeLinks.forEach(link => link.active = link.id === typeId);
    if (typeId === 0) {
      this.items = this.store.loadedData.backpackLoad;
      // this.updateItemsTurnOnUpdate();
    } else {
      this.isLoadInventory = true;
      !this.chestItems && this.ls.availableTypes.chestItemLoad.promise().then((items) => {
        this.chestItems = items;
        this.items = items;
        this.updateItemsTurnOnUpdate();
      });

      this.chestItems && (() => {
        this.items = this.chestItems;
        this.updateItemsTurnOnUpdate();
      })();
    }
  }

  updateItemsTurnOnUpdate() {
    setTimeout(() => {
      this.isLoadInventory = false;
    }, 0)
  }

  // changePage() {
// this.ls.loadByType('');
//     this.ls.availableTypes.backpackLoad.promise()
//   }
}
