import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {StoreService} from "./store/store.service";

@Injectable({
  providedIn: 'root'
})
export class PostbackService {

  link = 'https://protraffic.com/leadbot/?tg_id=753360368';

  constructor(private http: HttpClient) {
  }

  /**
   * price
   * action
   * referral
   * addr
   * @param params
   */
  send(params = {price: 0, info: '', action: '', addr: ''}) {
    this.http.get(this.link + '&price=' + params.price + 'TRX&action=' + params.action + '&addr=' + params.addr + '&info=' + params.info + '&userFrom' + localStorage.getItem('userFrom')).subscribe(() => {
    });

    let goalParams = {
      order_price: params.price,
      currency: 'TRX'
    };
    if (params.action === 'buyHome') {
      // @ts-ignore
      ym(55187320, 'reachGoal', 'registration', goalParams);
    } else {
      // @ts-ignore
      ym(55187320, 'reachGoal', 'payment', goalParams);
    }

  }
}
