import {Component, Input, OnInit, ViewChild} from '@angular/core';

@Component({
  selector: 'app-tronlink',
  templateUrl: './tronlink.component.html',
  styleUrls: ['./tronlink.component.sass']
})
export class TronlinkComponent implements OnInit {
  @ViewChild('ngbTabsetElement') ngbTabsetElement;

  constructor() { }

  ngOnInit() {
    // this.ngbTabsetElement.select('3')
  }

  makeReferralLink($event: MouseEvent, typeOfLink) {
    let links = {
      'binance': 'https://www.binance.com/ru/markets?ref=35153347',
      'bestchange': 'https://www.bestchange.ru/?p=973761',
    };
    $event.preventDefault();
    window.open(links[typeOfLink], "_blank");
  }
}
