import {Component, OnInit} from '@angular/core';
import {LocationService} from "../../services/location.service";
import {ContractService} from "../../../services/contract.service";
import {NotifyService} from "../../../services/notify.service";
import {LangService} from "../../services/lang.service";
import {TimeagoPipe} from "../../pipes/timeago.pipe";

@Component({
  selector: 'app-rating',
  templateUrl: './rating.component.html',
  styleUrls: ['./rating.component.sass']
})
export class RatingComponent implements OnInit {
  maxCountRating = 11;
  dataOfRating: any = {
    user: {},
    users: [],
    totalDayPoints: undefined,
    poll: undefined,
  };
  myDividendsCount: any = 0;
  loadingUserDividends: boolean = false;





  constructor(public locationService: LocationService, private cs: ContractService, private ns: NotifyService, public lang: LangService) {
  }

  ngOnInit() {
    this.fillUsersWithEmptyItems();
    this.cs.getRatingData().then(res => {
      this.dataOfRating = res;
      let bonusTime = new Date();
      bonusTime.setHours(new Date(this.dataOfRating.time).getHours());
      bonusTime.setMinutes(new Date(this.dataOfRating.time).getMinutes());
      bonusTime.setSeconds(new Date(this.dataOfRating.time).getSeconds());
      bonusTime.setDate(bonusTime.getDate() + 1);
      this.dataOfRating.time = bonusTime;
      console.log('bonusTime:', bonusTime);
      this.fillUsersWithEmptyItems();
    });
  }


  fillUsersWithEmptyItems() {
    if (this.dataOfRating.users.length < this.maxCountRating) {
      for (let i = this.dataOfRating.users.length; i < this.maxCountRating; i++) {
        this.dataOfRating.users.push(this.getEmptyItem());
      }
    }
  }

  knowMoreAboutDividends() {
    this.loadingUserDividends = true;
    this.cs.getUserDividendsCount().then((count) => {
      this.myDividendsCount = count;
      this.loadingUserDividends = false;
    });
  }


  getEmptyItem() {
    return {
      name: '-',
      place: '-',
      percent: '-',
      points: '-'
    }
  }

  getDividends() {
    this.loadingUserDividends = true;
    this.cs.getUserDividends().then((res: any) => {
      this.ns.success('     ' + this.cs.calcTrx(res.toNumber(), true) + ' TRX');
      this.knowMoreAboutDividends();
    }).catch(() => {
      this.knowMoreAboutDividends();
    });
  }
}
