import {Component, Input, OnInit} from '@angular/core';
import {BaseService} from "../../../services/base.service";
import {NotifyService} from "../../../services/notify.service";
import {HttpClient} from "@angular/common/http";
import {ContractService} from "../../../services/contract.service";

@Component({
  selector: 'app-adventure-history',
  templateUrl: './adventure-history.component.html',
  styleUrls: ['./adventure-history.component.sass']
})
export class AdventureHistoryComponent implements OnInit {
  @Input() userAddr;
  history: any = [];

  constructor(private bs: BaseService, private notify: NotifyService, private http: HttpClient, private cs: ContractService) {
  }

  ngOnInit() {
    this.cs.getHistoryOfAdventure().then(history => {
      this.history = history;
    });
  }




}
