import {Component, OnInit} from '@angular/core';
import {animate, style, transition, trigger} from "@angular/animations";
import {StoreService} from "../../../services/store/store.service";
import {faMinusCircle, faAngleDoubleUp, faChevronRight, faChevronLeft} from "@fortawesome/free-solid-svg-icons"
import {ContractService} from "../../../../services/contract.service";
import {HomeParams} from "../../../../interfaces/home-params";
import {HttpClient} from "@angular/common/http";
import {ModalService} from "../../../../services/modal.service";
import {LoadingService} from "../../../services/loading/loading.service";
import {FormControl, Validators} from "@angular/forms";
import {LangService} from "../../../services/lang.service";


@Component({
  selector: 'app-start',
  templateUrl: './start.component.html',
  styleUrls: ['./start.component.sass'],
  animations: [
    trigger('fadeInOut', [
      transition(':enter', [   // :enter is alias to 'void => *'
        style({height: 0}),
        animate(400, style({
          overflow: 'hidden',
          height: '*',
        }))
      ]),
      transition(':leave', [   // :leave is alias to '* => void'
        animate(400, style({
          opacity: '0',
          overflow: 'hidden',
          height: '0px',
        }))
      ])
    ])
  ]

})
export class StartComponent implements OnInit {
  faMinusCircle = faMinusCircle;
  faAngleDoubleUp = faAngleDoubleUp;
  faChevronRight = faChevronRight;
  faChevronLeft = faChevronLeft;
  hideRegistration = false;
  selectedHouseId = 0;
  houseList = [];
  userName: FormControl = new FormControl('', [
    Validators.pattern(/^[a-z][a-z0-9]*$/i),
    Validators.required,
    Validators.minLength(3),
    Validators.maxLength(15)
  ]);
  resources: any = ["food",
    "wood",
    "stone",
    "iron",
    "gold",
    "diamond"];
  home: any;


  constructor(public store: StoreService, private cs: ContractService, private http: HttpClient, private ms: ModalService, public ls: LoadingService, public lang: LangService) {
    this.resources = this.resources.map(resource => {
      return {
        key: resource,
        name: () => {
          return lang.translate(resource)
        }
      }
    });
    let valueFromLocalStorage = localStorage.getItem(store.nameOfLocalStorageKey);
    let storageRegData = valueFromLocalStorage ? JSON.parse(localStorage.getItem(store.nameOfLocalStorageKey)) || {} : {};
    if (storageRegData.tipId) {
      store.tipId = storageRegData.tipId;
      this.store.setHouseId(storageRegData.home);
      this.selectedHouseId = storageRegData.home ? storageRegData.home : 0;
      this.userName.setValue(storageRegData.name);
    }

  }

  ngOnInit() {
    // this.cs.
    //     
    this.houseList = this.store.loadedData.housesDemoList;
    this.home = this.selectedHouseId ? this.houseList[this.selectedHouseId] : this.houseList[0];
  }


  changeSelectedHome(idx) {
    if (idx < 0 || idx >= this.houseList.length) return false;
    this.selectedHouseId = idx;
    this.home = this.houseList[idx];
    this.store.setHouseId(idx);
  }


  nextTip() {
    if (this.store.tipId === 5) {
      this.changeSelectedHome(this.home.idx);
    }
    this.store.nextTip({home: this.selectedHouseId, name: this.userName.value});
  }

  openHelpModal() {
    this.ms.open({
      data: "",
      title: ' TronLink',
      componentName: 'app-tronlink'
    });
  }
}
