import {Component, Input, OnInit} from '@angular/core';
import {ItemService} from "../../../services/items/item.service";
import {ContractService} from "../../../services/contract.service";
import {LoadingService} from "../../services/loading/loading.service";
import {StoreService} from "../../services/store/store.service";
import {BaseService} from "../../../services/base.service";
import {ModalService} from "../../../services/modal.service";
import {FormControl, Validators} from "@angular/forms";
import {NotifyService} from "../../../services/notify.service";
import {LocationService} from "../../services/location.service";
import {LangService} from "../../services/lang.service";

@Component({
  selector: 'game-item',
  templateUrl: './game-item.component.html',
  styleUrls: ['./game-item.component.sass'],
  providers: [ModalService]
})
export class GameItemComponent implements OnInit {
  @Input() item;
  @Input() onSelect;
  @Input() resource;
  @Input() resourceCount;

  itemKey: any;
  durability: number = 100;
  durabilityColor: any = null;
  rimValues: any = {
    0: '',
    1: 'I',
    2: 'II',
    3: 'III',
    4: 'IV',
    5: 'V'
  };
  needShowMenu: boolean = false;
  itemBkgPosition: any;
  chestKey = null;

  constructor(public itemService: ItemService, private cs: ContractService, private ls: LoadingService, public store: StoreService, private bs: BaseService, private ms: ModalService, private ns: NotifyService, private locationService: LocationService, private lang: LangService) {
  }

  ngOnInit() {
    if (!this.item && !this.resource) return false;
    if (this.item && !this.item.isChest) {
      this.itemKey = this.itemService.getItemKey(this.item);
      this.item.itemParams = this.item.itemParams ? this.item.itemParams : {};
      this.durability = ((this.item.itemParams.durability) / (this.item.params[3].value + (this.store.loadedData.durabilityLevelCount * this.item.itemParams.charDurability))) * 100;
      this.durabilityColor = this.bs.redYellowGreen(0, 255, this.durability / 100 * 255);
    }

    // item bkg
    if (this.item && !this.item.isChest) this.itemBkgPosition = this.itemService.nameAndBkgPosOfItem[this.itemKey].backgroundPos;
    // resource bkg
    if (this.resource) this.itemBkgPosition = this.itemService.nameAndBkgPosOfItem.resources[this.resource].backgroundPos;

    // chest
    if (this.item && this.item.isChest) {
      let chestKeys = Object.keys(this.itemService.nameAndBkgPosOfItem.chest);
      this.chestKey = chestKeys[this.item.chestId];
      this.itemBkgPosition = this.itemService.nameAndBkgPosOfItem.chest[this.chestKey].backgroundPos;
    }
  }

  changeUseTypeItem(itemTypeUseById: any) {

    let loadTitleNames = {
      0: this.lang.translate('changeUseTypeItemLoadInBackpack'),
      1: this.lang.translate('changeUseTypeItemLoadInChest'),
      2: this.lang.translate('changeUseTypeItemLoadDress'),
    };
    this.ls.showLoadWithText(loadTitleNames[itemTypeUseById]);
    this.cs.changeUseTypeItem(this.item.itemParams.itemId, itemTypeUseById).then(res => {
      this.ls.loadByArrayType(['backpackLoad', 'dressedItems', 'chest'], undefined, true);
    }).catch(err => {
      this.ls.loadByArrayType(['backpackLoad', 'dressedItems', 'chest'], undefined, true);
    });

  }

  charItem() {
    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('enchantedItem'),
        description: this.lang.translate('improvementModalDescription'),
        callback: (result) => {
          this.ls.showLoadWithText(this.lang.translate('improvementModalLoadingDescription'));
          this.cs.charUserItem(this.item.itemParams.itemId).then(res => {
            this.ls.loadByArrayType(['backpackLoad', 'dressedItems', 'userInfo', 'chest'], undefined, true);
          }).catch(err => {
            this.ls.loadByArrayType(['backpackLoad', 'dressedItems', 'userInfo', 'chest'], undefined, true);
          });
          modal.close();
        }
      },
    });
  }

  openChest() {
    let getChestResultAfterOpen = async () => {
      let chest: any = await this.cs.getUserChestById(undefined, this.item.id);
      if (chest.result < 0) {
        return getChestResultAfterOpen();
      } else {
        return chest.result;
      }
    };


    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('openChest'),
        description: this.lang.translate('openChestDescription'),
        callback: (result) => {
          this.ls.showLoadWithText(this.lang.translate('openingChest'));
          this.cs.openUserChest(this.item.id).then(async () => {
            this.locationService.close();
            this.ls.loadByArrayType(['backpackLoad', 'userInfo'], async () => {
              let result: any = await getChestResultAfterOpen();

              this.store.results = {
                resources: this.bs.getEmptyResourcesValue(),
                item: null
              };

              this.store.loadedData.shop.forEach(chest => {
                if (chest.id === this.item.chestId) {
                  chest.loot.forEach((lootItem) => {
                    (result >= lootItem.percentFrom && result <= lootItem.percentTo) && (() => {
                      if (lootItem.itemId >= 0) this.store.results.item = this.store.loadedData.allItems.filter(item => item.id === lootItem.itemId)[0];
                      Object.keys(this.store.results.resources).forEach(resource => {
                        this.store.results.resources[resource] = lootItem[resource];
                      });
                    })();
                  });
                }
              });
              this.locationService.openResults();
            },  true);

          }).catch(err => {
            this.ls.isLoad = false;
          });
          modal.close();
        }
      },
    });
  }

  repairItem() {
    let wordsByMadeTypeId = {
      0: this.lang.translate('wood'),
      1: this.lang.translate('stone'),
      2: this.lang.translate('iron'),
      3: this.lang.translate('gold'),
      4: this.lang.translate('diamond')
    };
    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('fixGameItem'),
        description: this.lang.translate('fixItemDescription') + ' <span class="text-info">' + wordsByMadeTypeId[this.item.madeTypesId] + '</span>,' + this.lang.translate('continue') + '?',
        callback: (result) => {
          this.ls.showLoadWithText(this.lang.translate('fixGameItem') + '. ' + this.lang.translate('pleaseWait'));
          this.cs.repairItem(this.item.itemParams.itemId).then(res => {
            this.ls.loadByArrayType(['backpackLoad', 'userInfo'], undefined, true);
          }).catch(err => {
            this.ls.loadByArrayType(['backpackLoad', 'userInfo'], undefined, true);
          });
          modal.close();
        }
      },
    });
  }

  trowOutItem() {
    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('throwOut'),
        description: this.lang.translate('throwOut') + ' <span class="text-info">' + this.lang.translate(this.itemService.nameAndBkgPosOfItem[this.itemKey].key) + '</span>?',
        callback: (result) => {
          this.ls.showLoadWithText(this.lang.translate('throwOutItemDescription') + ' <span class="text-info">' + this.lang.translate(this.itemService.nameAndBkgPosOfItem[this.itemKey].key) + '</span>');
          this.cs.trowOutItem(this.item.itemParams.itemId).then(res => {
            this.ls.loadByArrayType(['backpackLoad', 'chest'], undefined, true);
          }).catch(err => {
            this.ls.loadByArrayType(['backpackLoad', 'chest'], undefined, true);
          });
          modal.close();
        }
      },
    });
  }

  tradeItem() {
    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate(this.itemService.nameAndBkgPosOfItem[this.itemKey].key),
        description: this.lang.translate('regItemToTradeIn1') + ' <span class="ml-2 text-info">' + this.store.loadedData['tradeCommission'] + ' TRX</span>. ' + this.lang.translate('regItemToTradeIn2'),
        form: new FormControl(0, [
          Validators.required,
          Validators.min(1)
        ]),
        callback: (result) => {
          if (result.form.valid === false) {
            if (result.form.errors.min) this.ns.info(this.lang.translate('validateErrorMin1'));
            return false;
          }
          this.ls.showLoadWithText(this.lang.translate('registrationAnItemInTradeIn'));
          this.cs.tradeItem(this.item.itemParams.itemId, result.form.value).then(() => {
            this.ls.loadByArrayType(['chest', 'backpackLoad'], undefined, true);
          }).catch(err => {
            this.ls.loadByArrayType(['chest', 'backpackLoad'], undefined, true);
          });
          modal.close();
        }
      },
    })
  }

  selectedItem() {
    if (!this.onSelect) {
      this.needShowMenu = true;
    } else {
      this.onSelect(this.item);
    }
  }
}
