import {Component, OnInit} from '@angular/core';
import {BaseService} from "../../../services/base.service";
import {faPlusCircle} from "@fortawesome/free-solid-svg-icons/faPlusCircle";
import {NotifyService} from "../../../services/notify.service";
import {HttpClient} from "@angular/common/http";
import {ContractService} from "../../../services/contract.service";

@Component({
  selector: 'app-chest',
  templateUrl: './chest.component.html',
  styleUrls: ['./chest.component.sass']
})
export class ChestComponent implements OnInit {
  templates: any = [];
  template: any = this.getEmptyTemplate();
  faPlusCircle: any = faPlusCircle;
  maxValue = 10000;
  hideOpenedChestsFlag: boolean = false;
  availableChests: any = [];
  myChests: any = [];

  constructor(private bs: BaseService, private notify: NotifyService, private http: HttpClient, private cs: ContractService) {
  }

  ngOnInit() {
    this.loadTemplates();
    this.loadAvailableChests();
    this.loadAllUserChests();
  }


  loadAvailableChests() {
    this.cs.loadAllChests().then((chests: any) => {
      this.availableChests = chests.map(chest => {
        let itemList = chest.loot.map(loot => {
          let emptyChest = this.getEmptyItemChest();
          emptyChest.percentFrom = loot.percentFrom;
          emptyChest.percentTo = loot.percentTo;
          emptyChest.itemId = loot.itemId;
          emptyChest.resources = emptyChest.resources.map(resource => {
            resource.value = loot[resource.name];
            return resource;
          });
          emptyChest.lootId = loot.lootId;
          return emptyChest;
        });

        return {
          id: chest.id,
          price: chest.price,
          itemList,
          chance: this.bs.getItemsCraftParamLootCharArrByKey('loot').map(dungeonChance => {
            dungeonChance.value = chest[dungeonChance.name];
            return dungeonChance;
          })
        }
      });
    })
  }


  loadAllUserChests() {
    this.cs.loadAllUserChests().then((chests: any) => {
      if (this.hideOpenedChestsFlag) {
        chests = chests.filter(chest => !chest.isOpened);
      }
      this.myChests = chests;
    });
  }

  getEmptyTemplate() {
    return {
      price: -1, //  
      chance: this.bs.getItemsCraftParamLootCharArrByKey('loot'), //    
      itemList: [this.getEmptyItemChest()]
    }
  }

  getEmptyItemChest(min = undefined) {
    return {
      itemId: -1,
      lootId: null,
      percentFrom: min || 0,
      percentTo: this.maxValue,
      type: 'item',
      resources: this.bs.getItemsCraftParamLootCharArrByKey('resources')
    }
  }

  getPercentFromMinAndMaxValues(min: number, max: number) {
    let value = (((max - min) / this.maxValue) * 100);
    value = isNaN(value) ? 0 : value;
    return value;
  }

  getResourcesTitle(resources: any) {
    let title = "";
    resources.forEach(resource => {
      title += resource.name + ':' + resource.value + ' ';
    });
    return title;
  }

  getPercentFromItems() {
    let percent = 0;
    this.template.itemList.forEach(item => {
      percent += this.getPercentFromMinAndMaxValues(item.percentFrom, item.percentTo);
    });
    return percent.toFixed(2);
  }

  copyModel() {
    this.bs.copyText(JSON.stringify(this.template));
    this.notify.success(' !');
  }


  loadTemplates() {
    this.http.get('assets/chest.json').subscribe((res) => {
      this.templates = res;
    });
  }

  uploadChestTemplate(template) {
    this.cs.uploadChestTemplate(template).then(res => {
      this.notify.success();
      this.loadAvailableChests();
    })
  }

  updateLootTemplate(chestId, lootId) {
    let lootParams = this.availableChests[chestId].itemList.filter(loot => loot.lootId === lootId)[0];
    let resources = this.bs.zipArray(lootParams.resources);

    this.cs.updateLootItem(chestId, lootId, [lootParams.percentFrom, lootParams.percentTo, lootParams.itemId, resources[0], resources[1], resources[2], resources[3], resources[4], resources[5]]).then(res => {

    });
  }

  updateChestMainInfo(chest) {
    let chanceParams = this.bs.zipArray(chest.chance);
    this.cs.updateChestTemplate(chest.id, chanceParams, chest.price).then((res) => {

    });
  }

  buyChest(id) {
    this.cs.buyChest(id).then(res => {
      this.notify.success('   !');
      this.loadAllUserChests();
    });
  }


  openChest(id) {
    this.cs.openUserChest(id).then(result => {
      this.loadAllUserChests();
    });
  }

  readTemplate(item) {
    this.template = item;
  }

  getRandomValue(value) {
    // setInterval(() => {
      this.cs.getRandomValue(value).then((res: number) => {
      });
    // }, 3000);
  }

  hideAndShowOpenedChests() {
    this.hideOpenedChestsFlag = !this.hideOpenedChestsFlag;
    this.loadAllUserChests()
  }
}
