import {Component, OnInit} from '@angular/core';
import {StoreService} from "../../services/store/store.service";
import {faArrowDown} from "@fortawesome/free-solid-svg-icons/faArrowDown";
import {BaseService} from "../../../services/base.service";
import {ModalService} from "../../../services/modal.service";
import {LoadingService} from "../../services/loading/loading.service";
import {ContractService} from "../../../services/contract.service";
import {LocationService} from "../../services/location.service";
import {NotifyService} from "../../../services/notify.service";
import {LangService} from "../../services/lang.service";

@Component({
  selector: 'app-home-improve',
  templateUrl: './home-improve.component.html',
  styleUrls: ['./home-improve.component.sass']
})
export class HomeImproveComponent implements OnInit {
  icons = {
    faArrowDown
  };


  opportunities = {};

  constructor(public store: StoreService, private bs: BaseService, private ms: ModalService, private ls: LoadingService, private cs: ContractService, public locationService: LocationService, private ns: NotifyService, public lang: LangService) {
    this.opportunities = { // key is level of home
      '2': [{
        name: this.bs.dungeons["3"].name,
        img: this.bs.dungeons["3"].img,
        isLocation: true,
      }],
      '3': [{
        name: this.bs.dungeons["2"].name,
        img: this.bs.dungeons["2"].img,
        isLocation: true,
      }, {
        name: this.lang.translate('enchantTable'),
        img: '/assets/game/base/interface/enchanted-table.png',
      }, {
        name: this.lang.translate('cowFarm'),
        img: '/assets/game/base/interface/cow.png',
      }], //  ,   ,  
      '4': [{
        name: this.lang.translate('anvil'),
        img: '/assets/game/base/interface/anvil.png',
      }, {
        name: this.lang.translate('chickenFarm'),
        img: '/assets/game/base/interface/chicken.png',
      }], // ,  
      '5': [{
        name: this.bs.dungeons["0"].name,
        img: this.bs.dungeons["0"].img,
        isLocation: true,
      }], //   , 
    };
  }

  improve() {
    let canImprove = true;
    this.store.loadedData.homeLoad.income.forEach(resource => {
      if (this.store.loadedData.userInfo[resource.name.toLowerCase()] < this.store.loadedData.loadLevelsData.values[this.store.loadedData.homeLoad.level]['cost' + resource.name]) {
        canImprove = false;
      }

    });
    if (!canImprove) {
      this.ns.warning(this.lang.translate('dontEnoughResourcesForImprovement'));
      return false;
    }


    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('homeImprovement'),
        description: this.lang.translate('homeImprovementModalDescription'),
        callback: (result) => {
          this.ls.showLoadWithText(this.lang.translate('pleaseWait'));
          this.cs.increaseHome().then(res => {
            this.ls.loadByArrayType(['userInfo', 'homeLoad'], () => {
              this.locationService.close();
              this.store.houseIdSubject.next(0);
            }, true);
          }).catch(err => {
            this.ls.loadByArrayType(['userInfo', 'homeLoad']);
          });
          modal.close();
        }
      },
    });

  }

  ngOnInit() {
  }

}
