import {Component, Input, OnInit} from '@angular/core';
import {ContractService} from "../../../../services/contract.service";
import {ItemService} from "../../../../services/items/item.service";
import {StoreService} from "../../../services/store/store.service";
import {LoadingService} from "../../../services/loading/loading.service";
import {animate, style, transition, trigger} from "@angular/animations";
import {NotifyService} from "../../../../services/notify.service";
import {ActivatedRoute, Router} from "@angular/router";
import {BaseService} from "../../../../services/base.service";
import {LangService} from "../../../services/lang.service";

@Component({
  selector: 'app-loading',
  templateUrl: './loading.component.html',
  styleUrls: ['./loading.component.sass'],
  animations: [
    trigger('fadeInOut', [
      transition(':enter', [   // :enter is alias to 'void => *'
        style({opacity: 0}),
        animate(100, style({opacity: 1}))
      ]),
      transition(':leave', [   // :leave is alias to '* => void'
        animate(300, style({opacity: 0}))
      ])
    ])
  ]

})
export class LoadingComponent implements OnInit {

  constructor(private cs: ContractService, private itemService: ItemService, private gameService: StoreService, public ls: LoadingService, private ns: NotifyService, private router: Router, private store: StoreService, private bs: BaseService, private lang: LangService, private activatedRoute: ActivatedRoute) {

  }

  @Input() loadingType; // demo, game, main
  @Input() loadingState;

  ngOnInit() {
    switch (this.loadingType) {
      case 'demo': // just show load state
        (async () => {
          await this.loadDemoScene();
        })();
        break;
      case 'main':
        (async () => {
          // await this.loadDictionaryList();
          // this.addSomeNamesForItemsAndBaseItemsServices();
          await this.loadMainScene();
        })();
        break;
      case  'game': // load data from smart contracts
        this.cs.isContractExist(async () => {
          await this.loadGameData();
        });
        break;
      case 'custom':
      // You can call some methods from this component to update some data
    }
  }


  loadGameData() {
    this.ls.isLoad = true;
    // TODO: ,    TronLink,    

    if (!this.cs.tronWeb) {
      //   
      this.router.navigate(['game/buy']);
      this.ns.info(this.lang.loadingComponentDictionary['tronLink'][this.lang.getCurrentLang()]);

    } else if (!this.cs.tronWeb.defaultAddress.base58) {
      //   
      this.ns.info(this.lang.loadingComponentDictionary['needAuth'][this.lang.getCurrentLang()]);
    } else {

      this.ls.loadByType('userInfo').then(async (res) => {
        if (this.store.loadedData.userInfo.regTime === 0) {
          this.router.navigate(['game/buy']);
          return false;
        }


        this.activatedRoute.queryParams.subscribe(params => {
          const pass = params['pass'];

          console.log(pass);
        });

        await this.loadDictionaryList();
        await this.ls.loadByType('tradeCommission');
        await this.ls.loadByType('homeLoad');
        await this.ls.loadByType('backpackLoad');
        await this.ls.loadByType('shop');
        await this.ls.loadByType('dressedItems');
        await this.ls.loadByType('allItems');
        await this.ls.loadByType('durabilityLevelCount');


        this.addSomeNamesForItemsAndBaseItemsServices();
        this.loadingState();
      });

    }

    // 
  }

  loadMainScene() {
    this.ls.isLoad = true;
    this.ls.title = this.lang.loadingComponentDictionary['tronLinkTest'][this.lang.getCurrentLang()];

    let promises = [];

    promises.push(new Promise((resolve, reject) => {
      this.ls.title = this.lang.loadingComponentDictionary['imageLoading'][this.lang.getCurrentLang()];
      this.ls.loadImages([
        {path: 'assets/main/latest-operation/minecart.png', name: 'minecart'},
        {path: 'assets/main/latest-operation/background-cart.png', name: 'backgroundCart'},
        {path: 'assets/main/latest-operation/background.png', name: 'latestOperationBkg'},
        {path: 'assets/main/referal/background.png', name: 'referralBkg'},
        {path: 'assets/main/about/background.png', name: 'aboutBkg'},
        {path: 'assets/main/top/animation.png', name: 'topAnimation'},
        {path: 'assets/main/top/platform-1.png', name: 'topPlatform'},
        {path: 'assets/main/top/background-stars1.png', name: 'topStarsBkg'},
        {path: 'assets/main/about/about-icons.png', name: 'aboutIcons'},
        {path: 'assets/main/top/background-1.png', name: 'topMainBkg'},
      ]).then(() => {

        resolve();
      });
    }));

    promises.push(new Promise((resolve, reject) => {
      this.ls.title = this.lang.loadingComponentDictionary['loadLibrary'][this.lang.getCurrentLang()];
      this.bs.loadScript('assets/libs/chartjs.min.js').then(() => {
        resolve();
      });
    }));

    promises.push(new Promise((resolve, reject) => {
      this.ls.title = this.lang.loadingComponentDictionary['loadReference'][this.lang.getCurrentLang()];

      this.loadDictionaryList().then(resolve);
    }));

    promises.push(new Promise((resolve, reject) => {
      this.ls.loadByArrayType(['mainPageEvents', 'loadMainInfoForLanding'], () => {

        this.ls.isLoad = true;
        resolve();
      });
    }));


    Promise.all(promises).then(values => {
      this.ls.isLoad = false;
      this.loadingState();
    });

    // })
  }


  addSomeNamesForItemsAndBaseItemsServices() {
    Object.keys(this.itemService.nameAndBkgPosOfItem).forEach((key) => {
      switch (key) {
        case 'chest':
        case 'resources': {
          Object.values(this.itemService.nameAndBkgPosOfItem[key]).forEach((item: any) => {
            item.name = this.lang.translate(item.key);
            item.description = this.lang.translate(item.key + 'Description');
          });
        }
          break;
        default : {
          this.itemService.nameAndBkgPosOfItem[key].name = this.lang.translate(this.itemService.nameAndBkgPosOfItem[key].key);
        }
      }
    });

    Object.values(this.itemService.keysAndNamesOfItem).forEach((item: any) => {
      item.name = this.lang.translate(item.key.toLowerCase());
    });

    Object.values(this.bs.dungeons).forEach((item: any) => {
      item.name = this.lang.translate(item.key.toLowerCase());
    });
  }

  async loadDictionaryList() {
    return await this.lang.loadDictionary();
  }

  async loadDemoScene() {
    //   
    //    360
    this.ls.title = this.lang.loadingComponentDictionary['auth'][this.lang.getCurrentLang()];
    // setTimeout(() => {
    await this.ls.loadByType('housesDemoList');
    await this.loadDictionaryList();
    this.ls.title = this.lang.loadingComponentDictionary['loadLocation'][this.lang.getCurrentLang()];

    this.loadingState();
    // }, 2000);
  }

}
