import {Component, OnInit} from '@angular/core';
import {UserInfo} from "../../../interfaces/user-info";
import {ContractService} from "../../../services/contract.service";
import {NotifyService} from "../../../services/notify.service";
import {Spinner} from "../../../interfaces/spinner";
import {uniqBy} from "lodash";

@Component({
  selector: 'app-info',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.sass']
})
export class InfoComponent implements OnInit {
  userInfo: UserInfo = null;
  spinners: any = {};


  loadUserOptions: any = {
    count: 5,
    start: 0
  };
  usersList: any = [];
  countOfUsers: number = 0;

  constructor(private cs: ContractService, private ns: NotifyService) {
    this.spinners['info'] = <Spinner>{loading: true, size: 'xs'};
  }

  ngOnInit() {
    this.loadInfo();
    this.loadUserList();
  }




  loadInfo() {
    this.spinners['info'].loading = true;
    this.cs.userInfo(null, true, true).then((res: UserInfo) => {
      this.userInfo = res;
      this.spinners['info'].loading = false;
    });
  }

  buySuperInvestor() {
    this.cs.buySuperInvestor().then(res => {
      this.loadInfo();
      this.ns.success();
    });
  }

  getDailyBonus() {
    this.spinners['info'].loading = true;
    this.cs.getDailyBonus().then(res => {
      this.loadInfo();
      this.ns.success();
    });
  }


  loadUserList() {
    this.cs.loadUsers(this.loadUserOptions.start, this.loadUserOptions.count).then((list: any) => {
      this.countOfUsers = list.lengthUsers;
      this.usersList = [...this.usersList, ...list.users];

      this.usersList = uniqBy(this.usersList, 'addr');
      this.loadUserOptions.start += this.loadUserOptions.count;
    });
  }

  giveUserLevel() {
    this.cs.giveUserLevel().then(res => {
      this.loadInfo();
    });
  }

  getDividends() {
    this.cs.getUserDividends().then((res: any) => {
      console.log('res:', this.cs.calcTrx(res.toNumber(), true));
    });
  }
}
