import {Component, OnInit} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {faInfoCircle, faTimes, faBan, faComments} from '@fortawesome/free-solid-svg-icons';
import {NotifyService} from "../../../services/notify.service";
import {ModalService} from "../../../services/modal.service";


@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.sass'],
  providers: [ModalService]
})
export class ChatComponent implements OnInit {
  price: any = 1;

  Object: any = Object;
  message: any;
  messages: any = [];
  moderList: any = [];
  bannedUserList: any = [];
  bannedUserHistory: any = null;
  typeList: any = [
    {name: '', idx: -1},
    {name: ' ', idx: 0},
    {name: ' ', idx: 1},
    {name: ' ', idx: 2}
  ];


  typeOfMessageSend: number = 0; //    
  typeOfMessageGet: number = -1;
  to: string = null;

  timeList: any = [];
  faInfoCircle: any = faInfoCircle;
  faTimes: any = faTimes;
  faBan: any = faBan;
  faComments: any = faComments;
  ban: any = {
    time: 1,
    reason: ''
  };

  //   

  constructor(private cs: ContractService, private ns: NotifyService) {
    let minute = 60000;
    let hour = minute * 60;
    this.timeList = {
      0: {id: 0, name: ' ', value: 0},
      1: {id: 1, name: '1 ', value: minute},
      2: {id: 2, name: '30 ', value: hour / 2},
      3: {id: 3, name: '1 ', value: hour},
      4: {id: 4, name: '2 ', value: hour * 2},
      5: {id: 5, name: '1 ', value: hour * 24},
      6: {id: 6, name: '1 ', value: (hour * 24) * 7},
      7: {id: 7, name: '1 ', value: (hour * 24) * 30},
      8: {id: 8, name: '', value: 99999999999}
    };


  }

  ngOnInit() {
    this.to = this.cs.ownerAddr;
    this.loadParams();
  }

  loadPrice() {
    (async () => {
      this.price = await this.cs.getMessagePrice();
    })();
  }

  getLatestMessages() {
    let timer = setTimeout(() => {
      this.getLatestMessages();
    }, 3000);

    this.cs.getMessagesList(5, this.typeOfMessageGet).then((res: any) => {
      clearTimeout(timer);
      res.map(item => {
        item.type = this.getMessageTypeById(item.messageType);
        return item;
      });

      this.messages = res;
    });
  }

  sendMessage() {
    this.cs.createMessage(this.message, this.typeOfMessageSend, this.to).then(res => {
      this.message = '';
      this.loadParams();
    });
  }

  getMessageTypeById(id) {
    let typeName = null;
    this.typeList.forEach(type => {
      if (id === type.idx) typeName = type.name;
    });
    return typeName;
  }

  changeMessagePrice() {
    this.cs.updateMessagePrice(this.price).then((res) => {
      this.loadParams();
    });
  }


  addModer(addr) {
    this.cs.addModer(addr).then(res => {
      this.ns.success();
      this.loadModerList();
    });
  }

  banUser(addr, reason, time, messageId, typeId) {
    let now = +new Date();
    time = now + this.timeList[time].value;
    time /= 1000;
    time = parseInt(time);

    this.cs.banUser(addr, reason, time, messageId, typeId).then((res) => {
      this.ns.success();
    });
  }

  loadModerList() {
    this.cs.getModerList().then(list => {
      this.moderList = list;
    });
  }

  loadBannedUsers() {
    this.cs.loadBannedUsers().then(list => {
      this.bannedUserList = list;
    });
  }

  loadParams() {
    this.getLatestMessages();
    this.loadPrice();
  }
}
