import {Component, Input, OnInit} from '@angular/core';
import {faArrowRight, faExchangeAlt} from "@fortawesome/free-solid-svg-icons";
import {ContractService} from "../../../services/contract.service";
import {StoreService} from "../../services/store/store.service";
import {ModalService} from "../../../services/modal.service";
import {LoadingService} from "../../services/loading/loading.service";
import {FormControl, Validators} from "@angular/forms";
import {NotifyService} from "../../../services/notify.service";
import {ItemService} from "../../../services/items/item.service";
import {LocationService} from "../../services/location.service";
import {LangService} from "../../services/lang.service";

@Component({
  selector: 'app-crafting-table',
  templateUrl: './crafting-table.component.html',
  styleUrls: ['./crafting-table.component.sass']
})
export class CraftingTableComponent implements OnInit {
  isChange = false;
  selectedItem = null;
  icons = {
    right: faArrowRight,
    exchange: faExchangeAlt
  };
  items = [];
  links = [];

  constructor(private cs: ContractService, private store: StoreService, private ms: ModalService, private ls: LoadingService, private ns: NotifyService, public locationService: LocationService, public lang: LangService) {
    this.links = [
      this.lang.translate('exchangeResourceWoodInDiamond'),
      this.lang.translate('exchangeResourceStoneInDiamond'),
      this.lang.translate('exchangeResourceIronInDiamond'),
      this.lang.translate('exchangeResourceGoldInDiamond'),
      this.lang.translate('exchangeResourceDiamondInExp')
    ];
  }

  ngOnInit() {
    this.items = this.store.loadedData.allItems;
    this.onItemSelect = this.onItemSelect.bind(this);
  }

  onItemSelect(item) {
    if (this.selectedItem && item.id === this.selectedItem.id) return false;
    this.selectedItem = null;
    setTimeout(() => {
      this.selectedItem = item;
    })
  }

  craftItem() {
    let canImprove = true;
    this.selectedItem.craft.forEach(resource => {
      if (this.store.loadedData.userInfo[resource.name] < resource.value) {
        canImprove = false;
      }
    });
    if (!canImprove) {
      this.ns.warning(this.lang.translate('dontEnoughResourcesForImprovement'));
      return false;
    }

    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('createAnItemBtnName') + '?',
        description: '',
        callback: (result) => {
          this.ls.showLoadWithText(this.lang.translate('createItemDescriptionOfLoad'));
          this.cs.itemCraft(this.selectedItem.id).then(res => {
            this.ls.loadByArrayType(['backpackLoad', 'dressedItems', 'userInfo'], undefined, true);
          });
          modal.close();
        }
      },
    });
  }


  changeResources(resourceId: number) {
    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('exchangeResourcesDescription'),
        description: this.lang.translate('areYouSure'),
        form: new FormControl(1, [
          Validators.required,
          Validators.min(1),
          Validators.max(100),
        ]),
        callback: (result) => {
          if (result.form.valid === false) {
            if (result.form.errors.min) this.ns.info(this.lang.translate('validateErrorMin1'));
            if (result.form.errors.max) this.ns.info(this.lang.translate('validateErrorMax100'));
            return false;
          }
          this.ls.showLoadWithText(this.lang.translate('exchangeResourcesDescription') + this.lang.translate('pleaseWait'));

          this.cs.changeResources(resourceId, result.form.value).then(() => {
            this.ls.loadByArrayType(['userInfo'], undefined, true);
            modal.close()
          });

        }
      },
    });
  }

}
