import {Component, OnInit} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {FormControl, FormGroup} from "@angular/forms";
import {HomeParams} from "../../../interfaces/home-params";
import {Spinner} from "../../../interfaces/spinner";
import {faTimes, faEdit, faSnowflake} from '@fortawesome/free-solid-svg-icons';
import {ContractHandlerService} from "../../../services/contract-handler.service";
import {NotifyService} from "../../../services/notify.service";
import {HttpClient} from "@angular/common/http";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.sass'],
  providers: [ContractService]
})
export class HomeComponent implements OnInit {
  spinners: any = {};
  HomeForm = new FormGroup({
    price: new FormControl('1'),
    wood: new FormControl('1'),
    stone: new FormControl('1'),
    food: new FormControl('8'),
    iron: new FormControl('1'),
    gold: new FormControl('1'),
    diamond: new FormControl('1')
  });

  editableParams: any = [
    {name: "diamond"},
    {name: "gold"},
    {name: "wood"},
    {name: "stone"},
    {name: "food"},
    {name: "iron"},
    {name: "price"}
  ];

  houses: HomeParams[] = [];
  home: any = null;
  userName: string = "";

  // TODO:    object
  faTimes = faTimes;
  faEdit = faEdit;
  faSnowflake = faSnowflake;
  refAddr: string = this.cs.emptyAddr;
  templateHouses: any = [];

  constructor(private cs: ContractService, private service: ContractHandlerService, private ns: NotifyService, private http: HttpClient) {
    this.spinners['my-house'] = <Spinner>{loading: true, size: 'xs'};
    this.spinners['houses'] = <Spinner>{loading: true, size: 'xs'};
  }

  ngOnInit() {
    this.loadHouses();
    this.loadMyHouseData();
    this.loadTemplateHouses();
  }

  addHouse(home = undefined) {
    let homeValues: HomeParams = home ? Object.assign({}, home) : Object.assign({}, this.HomeForm.value);
    homeValues.price = this.cs.calcTrx(homeValues.price);
    let values = [
      homeValues.price,
      homeValues.wood,
      homeValues.stone,
      homeValues.food,
      homeValues.iron,
      homeValues.gold,
      homeValues.diamond
    ];
    this.spinners['houses'].loading = true;

    this.cs.contract.addHouse(...values).send({
      callValue: this.cs.calcTrx(0),
      shouldPollResponse: true
    }).then((res) => {
      this.ns.success();
      this.loadHouses();
    }).catch((err) => {
      this.service.errorHandle(err);
      this.loadHouses();
      this.loadMyHouseData();
    });
  }


  loadTemplateHouses() {
    this.http.get('assets/houses.json').subscribe((res: any) => {
      this.templateHouses = res.map(home => {
        home.isTemplate = true;
        return home;
      });
    });
  }

  removeHouse(id: number) {
    this.spinners['houses'].loading = true;

    this.cs.contract.removeHouse(id).send({
      callValue: this.cs.calcTrx(0),
      shouldPollResponse: true
    }).then((res) => {
      this.loadHouses();
    }).catch((err) => {
      this.service.errorHandle(err);
      this.loadHouses();
      this.loadMyHouseData();
    });
  }

  loadHouses() {
    this.spinners['houses'].loading = true;
    this.cs.loadAvailableHouses().then((houses: HomeParams[]) => {
      this.spinners['houses'].loading = false;
      this.houses = houses;
    });
  }


  updateHouse(home: any) {
    this.spinners['houses'].loading = true;

    this.cs.contract.updateHouse(home.idx,
      this.cs.calcTrx(home.price),
      home.wood,
      home.stone,
      home.food,
      home.iron,
      home.gold,
      home.diamond
    ).send({
      callValue: this.cs.calcTrx(0),
      shouldPollResponse: true
    }).then((res) => {
      this.loadHouses();
    }).catch((err) => {
      this.service.errorHandle(err);
      this.loadHouses();
      this.loadMyHouseData();
    });
  }

  buyHouse(home: HomeParams) {
    this.spinners['my-house'].loading = true;
    this.spinners['houses'].loading = true;
    this.cs.buyHome({
      idx: home.idx, name: this.userName, ref: this.refAddr
    }).then(res => {
      this.spinners['houses'].loading = false;
      this.loadMyHouseData();
    });
  }


  loadMyHouseData() {
    this.spinners['my-house'].loading = true;
    this.cs.getUserHouse().then((res) => {
      this.home = res;
      this.spinners['my-house'].loading = false;
    }).catch(() => {
      this.spinners['my-house'].loading = false;
    });
  }

  increaseHome() {
    this.spinners['my-house'].loading = true;
    this.cs.increaseHome().then(res => {
      this.loadMyHouseData();
    });
  }
}
