import {Component, OnInit} from '@angular/core';
import {StoreService} from "../../services/store/store.service";
import {LocationService} from "../../services/location.service";

@Component({
  selector: 'app-result',
  templateUrl: './result.component.html',
  styleUrls: ['./result.component.sass']
})
export class ResultComponent implements OnInit {
  isEmptyResultResource: boolean = true;

  resources = ['food', 'wood', 'stone', 'iron', 'gold', 'diamond'];

  constructor(public store: StoreService, public locationService: LocationService) {
  }

  ngOnInit() {
    this.resources.forEach(resource => {
      if (this.store.results.resources[resource]) this.isEmptyResultResource = false;
    });

  }

}
