import {Component, HostListener, OnInit} from '@angular/core';
import {animate, style, transition, trigger} from "@angular/animations";
import {LoadingService} from "../../services/loading/loading.service";
import {StoreService} from "../../services/store/store.service";
import {LocationService} from "../../services/location.service";
import {ContractService} from "../../../services/contract.service";
import {LangService} from "../../services/lang.service";
import {ActivatedRoute, Router} from "@angular/router";
import {NotifyService} from "../../../services/notify.service";

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.sass'],
  host: {
    '(contextmenu)': 'disableClick($event)'
  },
  animations: [
    trigger('fadeInOut', [
      transition(':enter', [   // :enter is alias to 'void => *'
        style({opacity: 0}),
        animate(100, style({opacity: 1}))
      ]),
      transition(':leave', [   // :leave is alias to '* => void'
        animate(100, style({opacity: 0}))
      ])
    ])
  ]
})
export class MainComponent implements OnInit {
  loadingType = 'game';
  loadLocation: boolean = false;

  @HostListener('document:keydown', ['$event']) onKeydownHandler(event: KeyboardEvent) {
    switch (event.key.toLowerCase()) {
      case 'escape':
        this.locationService.close();
        break;
      case '':
      case 'e':
        // open/close inventory
        this.locationService.openInventory();
        break;
    }
  }

  loadingState: any = () => {
    this.loadLocation = true;
    this.ls.showLoadWithText(this.lang.loadingComponentDictionary['loadLocation'][this.lang.getCurrentLang()]);
  };


  sphereHasBeenLoaded: any = (turnLoad) => {
    !turnLoad && (this.ls.isLoad = false);
    turnLoad && (this.ls.showLoadWithText(this.lang.loadingComponentDictionary['loadLocation'][this.lang.getCurrentLang()]));
  };


  constructor(public ls: LoadingService, private store: StoreService, public locationService: LocationService, private cs: ContractService, private lang: LangService, private ns: NotifyService, private router: Router) {

  }

  ngOnInit() {
    this.cs.tronWeb.contract().at(this.cs.testNetAddr).then(async (testNetContract) => { // TODO:   
      this.cs.contract = testNetContract; //     

      document.body.style.overflow = 'hidden'; //        
      document.getElementsByClassName('main')[0] && document.getElementsByClassName('main')[0].classList.remove("main"); //        

    }).catch(e => {
      this.router.navigate(['']);
      this.ns.warning(this.lang.translate('tronNetworkValidateError'));
    });



  }


  disableClick(e) {
    e.preventDefault();
  }
}
