import {Component, Inject, Input, OnInit} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {Spinner} from "../../../interfaces/spinner";
import {TITLE} from "../../../services/base.service";

@Component({
  selector: 'app-moder-info',
  templateUrl: './user-info.component.html',
  styleUrls: ['./user-info.component.sass']
})
export class UserInfoComponent implements OnInit {
  @Input() data;
  address: string = null;
  user: any = null;
  loading: any = <Spinner>{loading: true, size: 'xs'};
  toggleClaim: boolean;
  toggleThanks: boolean;


  constructor(private cs: ContractService, @Inject(TITLE) private injectedModalData: any) {

  }

  ngOnInit() {
    this.address = this.injectedModalData.data;
    this.loadModalData();
  }


  loadModalData() {
    this.toggleClaim = false;
    this.toggleThanks = false;

    let requestPromises = {
      userInfo: {loadFn: () => this.cs.userInfo(this.address, false), promise: null, status: false},
      moderInfo: {loadFn: () => this.cs.loadModerInfo(this.address, true), promise: null, status: false},
      banInfo: {loadFn: () => this.cs.loadBannedUserInfo(this.address), promise: null, status: false},
      deletedMessages: {loadFn: () => this.cs.getDeletedMessagesByModer(this.address), promise: null, status: false},
      thankList: {loadFn: () => this.cs.loadThankList(this.address), promise: null, status: false},
      claimList: {loadFn: () => this.cs.loadClaimListList(this.address), promise: null, status: false},
    };

    let requestValues = Object.values(requestPromises);

    requestValues.forEach((request, idx) => {
      requestValues[idx].promise = new Promise(res => {

        function loadData() {
          let timeoutRequest = setTimeout(() => {
            if (requestValues[idx].status) return false;
            console.log('  ,   !: ' + idx);
            loadData();
          }, 1000);

          requestValues[idx].loadFn().then(result => {
            clearTimeout(timeoutRequest);
            requestValues[idx].status = true;
            res(result);
          });
        }


        loadData();
      });
    });

    Promise.all(requestValues.map(req => req.promise)).then(([userInfo, moderInfo, banInfo, deletedMessages, thankList, claimList]) => {
      this.user = Object.assign(userInfo, {moder: moderInfo}, {addr: this.address}, {ban: banInfo}, {deletedMessages}, {thankList}, {claimList});
      this.loading.loading = false;
    });

  }

  updateCallback() {
    this.loadModalData();
  }


}
