import {Component, OnInit} from '@angular/core';
import {LocationService} from "../../services/location.service";
import {ModalService} from "../../../services/modal.service";
import {LoadingService} from "../../services/loading/loading.service";
import {ContractService} from "../../../services/contract.service";
import {StoreService} from "../../services/store/store.service";
import {LangService} from "../../services/lang.service";

@Component({
  selector: 'app-interface-items',
  templateUrl: './interface-items.component.html',
  styleUrls: ['./interface-items.component.sass']
})
export class InterfaceItemsComponent implements OnInit {
  icons: any = [];

  constructor(private locationService: LocationService, private ms: ModalService, private ls: LoadingService, private cs: ContractService, private store: StoreService, private lang: LangService) {

  }


  ngOnInit() {
    this.updateIcons();
  }


  updateIcons() {
    this.icons = [
      {
        'title': '',
        isAvailable: true,
        iconPosition: '-1024px 0',
        callback: () => {
          this.locationService.toggle('rating');

        }
      },
      {
        'title': '',
        isAvailable: true,
        iconPosition: '-768px 0',
        callback: () => {
          this.locationService.openInventory();
        }
      },{
        'title': '',
        isAvailable: true,
        iconPosition: '-384px 0',
        callback: () => {
          this.locationService.openDungeons();
        }
      }, {
        'title': '',
        isAvailable: true,
        iconPosition: '-256px 0',
        htmlRender: () => {
          let now = +new Date();
          let isLessThanNow = this.store.loadedData.userInfo.bonusTime < now;
          // @ts-ignore
          let timeAgoFormat = timeago.format(this.store.loadedData.userInfo.bonusTime, this.lang.userLang);
          return isLessThanNow ? `<span class="text-warning position-absolute w-100 h6 interface-icons-list-time mb-0">${this.lang.translate('done')}</span>`: `<span class="text-warning position-absolute w-100 h6 interface-icons-list-time mb-0">${timeAgoFormat}</span>`
        },
        callback: () => {
          let modal = this.ms.openMineCraftAlert({
            data: {
              title: this.lang.translate('dailyBonus'),
              description: this.lang.translate('getDailyBonus'),
              callback: (result) => {
                this.ls.showLoadWithText(this.lang.translate('getDailyBonusLoadDescription'));
                this.cs.getDailyBonus().then(res => {
                  this.ls.loadByArrayType(['userInfo'], undefined, true);
                }).catch(err => {
                  this.ls.isLoad = false;
                });
                modal.close();
              }
            },
          })
        }
      }, {
        'title': '',
        isAvailable: true,
        iconPosition: '-128px 0',
        callback: () => {
          this.locationService.openTrade();
        }
      }
      ,      {
        'title': '',
        isAvailable: this.store.loadedData.homeLoad.level <= 4,
        iconPosition: '-512px 0',
        html: '<div class="d-inline-block rounded-circle position-absolute text-white interface-icons-list-icon-additional-house">' + this.store.loadedData.homeLoad.level + ' lvl</div>',
        callback: () => {
          this.locationService.openHomeImprove();
        }
      }, {
        'title': '',
        isAvailable: true,
        html: '<div class="d-inline-block position-absolute interface-icons-list-icon-partner">' + (this.store.loadedData.userInfo.superInvestorPercent ? this.store.loadedData.userInfo.superInvestorPercent : '10') + '%</div>',
        iconPosition: '0 0',
        callback: () => {
          this.locationService.openReferralProgram();
        }
      }, {
        'title': '',
        isAvailable: true,
        iconPosition: '-640px 0',
        callback: () => {
          this.locationService.openShop();
        }
      }
      // {
      //
      //   'title': 'FAQ',
      //   isAvailable: true,
      //   iconPosition: '-896px 0',
      //   callback: () => {
      //     this.locationService.toggle('faq');
      //   }
      // }
    ]

  }

}
