import {Component, OnInit} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {Spinner} from "../../../interfaces/spinner";
import {timer} from "rxjs";
import {ContractHandlerService} from "../../../services/contract-handler.service";
import {Levels} from "../../../interfaces/levels";
import {NotifyService} from "../../../services/notify.service";
import {Investor} from "../../../interfaces/investor";
import {HttpClient} from "@angular/common/http";

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.sass']
})
export class SettingsComponent implements OnInit {
  levels: Levels[] = [];
  spinners: any = {};
  selectedLevel: number = 0;
  tradeCommission: number = 0;
  templateOfLevels: any = [];
  Object: any = Object;
  investor: Investor = {price: 0, available: 0, percent: 0};


  constructor(public cs: ContractService, private ns: NotifyService, private service: ContractHandlerService, private http: HttpClient) {
    this.spinners['super-investor'] = <Spinner>{loading: true, size: 'xs'};
    this.spinners['house-level'] = <Spinner>{loading: true, size: 'xs'};
  }

  ngOnInit() {
    this.loadInvestorInfo();
    this.loadTemplateData();
    this.loadHomeLevelData();

    this.cs.loadTradeCommissionPrice().then((price: number) => {
      this.tradeCommission = this.cs.calcTrx(price, true);
    });
  }


  loadTemplateData() {
    this.http.get('assets/house-improve-params.json').subscribe((levelData: any) => {
      this.templateOfLevels = levelData;
    });
  }

  loadHomeLevelData() {
    this.levels = [];
    this.spinners['house-level'].loading = true;
    this.cs.loadLevelsData().then((result: any) => {
      result.values.forEach((item) => {
        let cost = {};
        let increase = {};
        Object.keys(item).forEach((key) => {
          let valueByKey = item[key];
          key.indexOf('cost') != -1 && (() => {
            let keyVal = key.split('cost')[1].toLowerCase();
            if (keyVal === 'trx') valueByKey = this.cs.calcTrx(valueByKey, true);
            cost[keyVal] = {key: keyVal, value: valueByKey}
          })();
          key.indexOf('increase') != -1 && (() => {
            let keyVal = key.split('increase')[1].toLowerCase();
            increase[keyVal] = {key: keyVal, value: valueByKey};
          })();
        });
        this.levels.push({
          idx: item.idx,
          title: (item.idx + 1) + '- ',
          available: item.available,
          cost,
          increase
        });
      });

      this.spinners['house-level'].loading = false;
    });
  }


  unlockOrLockLevelHouse() {
    this.spinners['house-level'].loading = true;
    this.cs.unlockOrLockLevelHouse(this.levels[this.selectedLevel].idx, !this.levels[this.selectedLevel].available).then(resolve => {
      this.ns.success();
      this.loadHomeLevelData();
    }).catch(err => {
      this.loadHomeLevelData();
    });
  }

  setDailyIncoming(levelData = undefined) {
    //uint id, uint costWood, uint costIron, uint costGold, uint costEat, uint costTrx
    let level = levelData ? levelData : this.levels[this.selectedLevel];
    this.spinners['house-level'].loading = true;

    let params = {idx: level.idx};
    Object.values(level.increase).forEach((cost: any) => {
      params[cost.key] = cost.value;
    });

   return this.cs.setDailyIncoming(params).then((res) => {
      this.spinners['house-level'].loading = false;
      this.ns.success();
    });
  }

  setLevelPrice(levelData = undefined) {
    //uint id, uint costWood, uint costIron, uint costGold, uint costEat, uint costTrx
    let level = levelData ? levelData : this.levels[this.selectedLevel];
    this.spinners['house-level'].loading = true;

    let params = {idx: level.idx};

    Object.values(level.cost).forEach((cost: any) => {
      let value = cost.value;
      if (cost.key === 'trx') value = this.cs.calcTrx(cost.value);
      params[cost.key] = value;
    });

    return this.cs.setLevelPrice(params).then((res) => {
      this.spinners['house-level'].loading = false;
      this.ns.success();
    });
  }


  updateSuperInvestorPrice() {
    this.spinners['super-investor'].loading = true;
    this.cs.updateSuperInvestorPrice(this.investor.price).then(result => {
      timer(1000).subscribe(x => this.loadInvestorInfo());
    });
  }

  loadInvestorInfo() {
    this.spinners['super-investor'].loading = true;
    this.cs.getSuperInvestorInfo().then((result: any) => {
      this.investor.price = result.price;
      this.investor.available = result.available;
      this.investor.percent = result.percent;
      this.spinners['super-investor'].loading = false;
    });
  }

  updateSuperInvestorAvailable() {
    this.spinners['super-investor'].loading = true;

    this.cs.updateSuperInvestorAvailable(!!this.investor.available).then(result => {
      timer(1000).subscribe(x => this.loadInvestorInfo());
      this.ns.success();
    });
  }

  updateSuperInvestorPercent() {
    this.spinners['super-investor'].loading = true;
    this.cs.superInvestorPercentUpdate(this.investor.percent).then(result => {
      timer(1000).subscribe(x => this.loadInvestorInfo());
      this.ns.success();
    });
  }

  updateDailyData(homeLevel = undefined) {
    Promise.all([this.setDailyIncoming(homeLevel), this.setLevelPrice(homeLevel)]).then((res) => {
      this.loadHomeLevelData();
    });
  }
}
