import {Component, OnInit, HostListener, ViewChild, AfterViewInit} from '@angular/core';
import {BaseService} from "../../../services/base.service";
import {StoreService} from "../../services/store/store.service";
import {ItemService} from "../../../services/items/item.service";
import {ContractService} from "../../../services/contract.service";
import {LoadingService} from "../../services/loading/loading.service";
import {ModalService} from "../../../services/modal.service";
import {LocationService} from "../../services/location.service";
import {LangService} from "../../services/lang.service";

@Component({
  selector: 'app-trade-list',
  templateUrl: './trade-list.component.html',
  styleUrls: ['./trade-list.component.sass']
})
export class TradeListComponent implements OnInit, AfterViewInit {
  @ViewChild('tradeContent') tradeContent;
  @ViewChild('paginationBlock') paginationBlock;
  @ViewChild('filterButtons') filterButtons;

  @HostListener('window:resize', ['$event']) onResize(event) {
    this.pasteElementsForPage();
  }

  countOfTradeItems = 0;
  tableHeight: number = null;

  page: number = 1;
  elements: any = [];
  loadMyItems: boolean = false;


  constructor(private bs: BaseService, public store: StoreService, private itemService: ItemService, private cs: ContractService, private ls: LoadingService, private ms: ModalService, public locationService: LocationService, private lang: LangService) {
  }

  ngOnInit() {

  }

  calcElementsHeight() {
    let contentBorderWidth = parseInt((window.getComputedStyle(<HTMLElement>this.tradeContent.nativeElement).borderTopWidth)) + parseInt((window.getComputedStyle(<HTMLElement>this.tradeContent.nativeElement).borderBottomWidth));
    let contentPadding = parseInt((window.getComputedStyle(<HTMLElement>this.tradeContent.nativeElement).paddingBottom)) + parseInt((window.getComputedStyle(<HTMLElement>this.tradeContent.nativeElement).paddingTop));
    let heightOfContent = (<HTMLElement>this.tradeContent.nativeElement).getBoundingClientRect().height - contentPadding - contentBorderWidth;

    let heightOfPagination = this.bs.getHeightWithMarginOfElement(this.paginationBlock.nativeElement);


    let heightOfButtons = this.bs.getHeightWithMarginOfElement(this.filterButtons.nativeElement);

    let trHeight = 55.33;

    this.tableHeight = heightOfContent - heightOfButtons  - heightOfPagination - 24;
    this.countOfTradeItems = Math.ceil(this.tableHeight / trHeight) - 1;
  }

  pasteElementsForPage() {
    this.calcElementsHeight();
    if (this.countOfTradeItems <= 0) return false;
    this.elements = [...Array(this.countOfTradeItems).keys()].map((item, idx) => {
      idx = (this.countOfTradeItems * this.page - this.countOfTradeItems) + idx;
      let tradeItem = this.store.loadedData.tradeItemList[idx];
      if (!tradeItem || (this.loadMyItems && tradeItem.owner !== this.cs['tronWeb'].defaultAddress.hex) || (!this.loadMyItems && tradeItem.owner === this.cs['tronWeb'].defaultAddress.hex)) return undefined;
      return tradeItem;
    }).sort(item => item);
  }


  ngAfterViewInit() {
    setTimeout(() => {
      this.pasteElementsForPage();
    }, 0);
  }


  buyItem(item) {
    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('productPurchase'),
        description: this.lang.translate('areYouSureWantToBuy') + ' <span class="text-info">' + item.info.name + '</span>?',
        callback: (result) => {
          this.ls.showLoadWithText(this.lang.translate('pleaseWait'));
          this.cs.buyTradeItem(item).then(res => {
            this.ls.showLoadWithText(this.lang.translate('successUpdateUserData'));
            this.ls.loadByArrayType(['backpackLoad', 'tradeItemList'], undefined, true);
          }).catch(err => {
            this.ls.isLoad = false;
          });
          modal.close();
        }
      },
    });


  }

  updateItemList(isMyList) {
    this.loadMyItems = isMyList;
    this.pasteElementsForPage();
  }

  takeOutFromTrade(item) {
    let modal = this.ms.openMineCraftAlert({
      data: {
        title: this.lang.translate('pickUpModalItemTitle'),
        description: this.lang.translate('pickUpModalItemDescription') + ' <span class="text-info">' + item.info.name + '</span> ' + this.lang.translate('fromAuction') + '?',
        callback: (result) => {
          this.ls.showLoadWithText(this.lang.translate('auctionPickUpItemFromAuctionLoadingDescription'));
          this.cs.tradeItemOut(item.userTradeItemId).then(res => {
            this.ls.showLoadWithText(this.lang.translate('successUpdateUserData'));
            this.ls.loadByArrayType(['backpackLoad', 'tradeItemList'], undefined, true);
          }).catch(err => {
            this.ls.isLoad = false;
          });
          modal.close();
        }
      },
    });

  }
}
