import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {NotFoundComponent} from './components/not-found/not-found.component';
import {ContractService} from "./services/contract.service";
import {SnotifyModule, SnotifyService, ToastDefaults} from 'ng-snotify';
import {ContractHandlerService} from "./services/contract-handler.service";
import {JsonPipe} from './pipes/json.pipe';
import {HttpClientModule} from "@angular/common/http";
import {ModalService, ModalTemplate} from "./services/modal.service";
import {UserInfoComponent} from './components/modals/user-info/user-info.component';
import {NgbPaginationModule, NgbTabsetModule} from "@ng-bootstrap/ng-bootstrap";
import {AdminModule} from "./admin/admin.module";
import {FontAwesomeModule} from "@fortawesome/angular-fontawesome";
import { TronlinkComponent } from './components/modals/tronlink/tronlink.component';
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {AlertComponentComponent} from "./game/components/alerts/alert-component/alert-component.component";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {GameModule} from "./game/game.module";

// import {each}
//
@NgModule({
  declarations: [
    AppComponent,
    NotFoundComponent,
    JsonPipe,
    ModalTemplate,
    UserInfoComponent, TronlinkComponent, AlertComponentComponent
  ],
  imports: [
    BrowserAnimationsModule,
    BrowserModule,
    AppRoutingModule,
    SnotifyModule,
    HttpClientModule,
    NgbPaginationModule,
    AdminModule,
    FontAwesomeModule,
    NgbTabsetModule,
    FormsModule,
    ReactiveFormsModule,
    GameModule
  ],
  providers: [ContractService, ContractHandlerService,
    {provide: 'SnotifyToastConfig', useValue: ToastDefaults}, SnotifyService, ModalService],
  exports: [
  ],
  bootstrap: [AppComponent],
  entryComponents: [ModalTemplate, UserInfoComponent, TronlinkComponent, AlertComponentComponent]
})
export class AppModule {
}
