import {Component, Input, OnInit} from '@angular/core';
import {animate, style, transition, trigger} from "@angular/animations";
import {StoreService} from "../../../services/store/store.service";
import {ItemService} from "../../../../services/items/item.service";

@Component({
  selector: 'app-panel-game',
  templateUrl: './panel.component.html',
  styleUrls: ['./panel.component.sass'],
  animations: [
    trigger('fadeInOut', [
      transition(':enter', [   // :enter is alias to 'void => *'
        style({opacity: 0}),
        animate(100, style({opacity: 1}))
      ]),
      transition(':leave', [   // :leave is alias to '* => void'
        animate(300, style({opacity: 0}))
      ])
    ])
  ]

})
export class PanelComponent implements OnInit {
  isShowItemName: boolean = true;
  itemName: string = '';
  itemNameTimeout: any = null;
  resourcesKeys = null;


  constructor(private store: StoreService, private itemService: ItemService) {
  }

  ngOnInit() {
    this.resourcesKeys = Object.keys(this.itemService.nameAndBkgPosOfItem.resources);
  }

  mouseOverOfItem(key) {
    this.itemNameTimeout && clearTimeout(this.itemNameTimeout);
    this.isShowItemName = true;
    let dressedItemByKey = this.store.dressedItems[key];
    let isItemKey = (key === 'sword' || key === 'hoe' || key === 'pickaxe');
    this.itemName = isItemKey && dressedItemByKey ?
      this.itemService.nameAndBkgPosOfItem[this.itemService.getItemKey(dressedItemByKey)].name : isItemKey && !dressedItemByKey ? '' : // get item name by key
      this.itemService.nameAndBkgPosOfItem.resources[key].name; //
    this.itemName === undefined && (this.itemName = '');
    this.hideItemName();
  }


  hideItemName() {
    this.itemNameTimeout = setTimeout(() => {
      this.isShowItemName = false;
    }, 2000)
  }
}
