import {Component, OnInit} from '@angular/core';
import {ContractService} from "../../../services/contract.service";
import {BaseService} from "../../../services/base.service";
import {NotifyService} from "../../../services/notify.service";
import {HttpClient} from "@angular/common/http";
import {ItemService} from "../../../services/items/item.service";

@Component({
  selector: 'app-trade',
  templateUrl: './trade.component.html',
  styleUrls: ['./trade.component.sass']
})
export class TradeComponent implements OnInit {
  tradeList: any = [];
  itemsList: any = [];
  showSoldElements: boolean = false;
  createItemParam: any;
  paramsOfCreateItems: any;

  constructor(private cs: ContractService, private bs: BaseService, private notify: NotifyService, private http: HttpClient, private itemService: ItemService) {
    this.createItemParam = itemService.getEmptyItem();
    this.paramsOfCreateItems = itemService.getParamsOfCreate();
  }

  ngOnInit() {
    this.loadElements();
  }


  loadElements(toggleSoldElements = undefined) {
    toggleSoldElements && (this.showSoldElements = !this.showSoldElements);
    this.tradeList = [];
    let promises = [];
    promises.push(new Promise((resolve, reject) => {
      this.cs.loadAvailableItems().then(resolve);
    }));

    promises.push(new Promise((resolve, reject) => {
      this.cs.loadAllTradeItems().then(resolve);
    }));
    Promise.all(promises).then(([itemList, tradeList]) => {
      this.itemsList = itemList.reduce((acc, item) => {
        if (!acc[item.id]) acc[item.id] = {};
        acc[item.id].typeName = this.createItemParam.params[item.type].name;
        acc[item.id].madeName = this.bs.getMadeTypeById(item.madeTypesId, true);
        acc[item.id].armourName = this.bs.getArmorTypeById(item.armorTypeId, true);
        acc[item.id].toolName = this.bs.getToolTypeById(item.toolTypeId, true);
        return acc;
      }, {});


      let promisesOfItemUsers = [];
      tradeList.forEach(trade => {
        promisesOfItemUsers.push(
          new Promise((resolve, reject) => {
            this.cs.itemUserList(trade.owner, trade.itemUserId).then(resolve);
          })
        );
      });

      Promise.all(promisesOfItemUsers).then(userItemList => {
        userItemList.forEach((userItem, idx) => {
          tradeList[idx].info = userItem;
        });
        if (!this.showSoldElements) {
          tradeList = tradeList.filter(tradeItem => {
            return tradeItem.available;
          });
        }

        this.tradeList = tradeList;
      });
    });
  }

}
