import {Component, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import {faAngleDoubleLeft, faAngleDoubleRight} from "@fortawesome/free-solid-svg-icons";
import {LangService} from "../../services/lang.service";

@Component({
  selector: 'app-items-list',
  templateUrl: './items-list.component.html',
  styleUrls: ['./items-list.component.sass']
})
export class ItemsListComponent implements OnInit, OnChanges {
  @Input() items;
  @Input() maxCount;
  @Input() OnSelectedItem;
  @Input() availableTotalItems; // 155   310  .
  @Input() totalItems; //    ,      200 ,    150.
  @Input() isLoadItems: boolean = false;

  itemsList = [];
  page: number = 1;
  pages: any = [{active: false, icon: faAngleDoubleLeft}, {active: false, icon: faAngleDoubleRight}];

  constructor(public lang: LangService) {
  }

  ngOnInit() {
    this.loadItemsByPage();
  }

  ngOnChanges(changes: SimpleChanges): void {
   this.page = 1;
   this.loadItemsByPage();
  }

  loadItemsByPage() {
    this.itemsList = [...Array(this.maxCount).keys()].map((item, idx) => {
      idx = (this.maxCount * this.page - this.maxCount) + idx;
      return this.items[idx];
    });

    this.calcNextAndPrevPagesActiveStatus();
  }


  calcNextAndPrevPagesActiveStatus() {
    this.pages[0].active = this.page > 1; //prev
    this.pages[1].active = this.items.length > this.page * this.maxCount; //next
  }

  changePage(idx) { // 0 - prev, 1 - next
    let values = {
      0: this.page - 1,
      1: this.page + 1,
    };

    let value = values[idx];
    if (value * this.maxCount - this.maxCount > this.items.length) return false;
    this.page = value;
    this.loadItemsByPage();
  }
}
