import {Injectable} from '@angular/core';
import {LoadingService} from "./loading/loading.service";
import {ContractService} from "../../services/contract.service";
import {BaseService} from "../../services/base.service";
import {StoreService} from "./store/store.service";

@Injectable({
  providedIn: 'root'
})
export class LocationService {
  elements: any = {
    inventory: false,
    craftingTable: false,
    chest: false,
    charTable: false,
    chat: false,
    trade: false,
    dungeons: false,
    referral: false,
    shop: false,
    faq: false,
    rating: false,
    result: false,  // result after dungeon or open chest and etc
    home: false
  };

  constructor(private ls: LoadingService, private cs: ContractService, private bs: BaseService, private store: StoreService) {
  }

  toggle(key) {
    this.close(key);
    this.elements[key] = !this.elements[key];
  }

  close(noNeedCloseKey = undefined) {
    Object.keys(this.elements).forEach(key => {
      if (noNeedCloseKey !== key) this.elements[key] = false;
    });
  }


  openTrade() {
    this.ls.isLoad = true;
    this.ls.loadByType('tradeItemList').then(res => {
      this.elements.trade = true;
      this.ls.isLoad = false;
    });

  }

  openDungeons() {
    this.ls.isLoad = true;
    this.ls.loadByArrayType(['dungeonHistory', 'adventureTimes'], () => {
      this.elements.dungeons = true;
      this.ls.isLoad = false;
    });
  }

  openReferralProgram() {
    this.ls.isLoad = true;
    this.ls.loadByType('partnerInfo').then(() => {
      this.elements.referral = true;
      this.ls.isLoad = false;
    });
  }


  openChest() {
    this.ls.isLoad = true;
    this.ls.loadByType('chest').then(res => {
      this.ls.isLoad = false;
      this.elements.chest = true;
    });
  }

  openHomeImprove() {
    this.ls.isLoad = true;
    this.ls.loadByType('loadLevelsData').then(res => {
      this.ls.isLoad = false;
      this.elements.home = true;
    });
  }

  openInventory() {
    if (!this.elements.chat) this.toggle('inventory');
  }

  openShop() {
    this.ls.isLoad = true;
    this.ls.loadByType('shop').then(res => {
      this.ls.isLoad = false;
      this.elements.shop = true;
    })
  }

  openResults() {
    this.close();
    this.elements.result = true;
  }

  goToAdventure(type, count = 1) {
    let availableTypes = {
      'inmine': {
        id: 1, messages: [
          '  !   , ,  !?',
          '       !',
          '  !?  !       . , . '
        ]
      },
      'inhell': {
        id: 2,
        messages: ['   ,  !?      .', '      ', ' , ,    .']
      },
      'inedge': {
        id: 0,
        messages: ['     .', ', ,    -   .', '- ,     ,     .']
      },
      'infarm': {
        id: 3,
        messages: ['    ,   ', ', , , ,   .', '          .']
      },
    };


    this.ls.showLoadWithText(availableTypes[type].messages[this.bs.getRandomInt(0, availableTypes[type].messages.length - 1)]);
    this.cs.goToAdventure(availableTypes[type].id, count).then(() => {
      this.ls.loadByArrayType(['backpackLoad', 'dressedItems', 'userInfo', 'dungeonHistory', 'adventureTimes'], () => {
        let latestDungeon = this.store.loadedData.dungeonHistory[0];
        this.store.results = {
          item: null,
          resources: this.bs.getEmptyResourcesValue()
        };



        Object.keys(this.store.results.resources).forEach(resource => {
          this.store.results.resources[resource] = latestDungeon[resource];
        });
        this.openResults();
      }, true);
    }).catch(err => {
      this.ls.isLoad = false;
    });
  }

}
