import {Component, Inject, Input, OnInit} from '@angular/core';
import {NgbModalRef} from "@ng-bootstrap/ng-bootstrap";
import {TITLE} from "../../../../services/base.service";

@Component({
  selector: 'app-alert-component',
  templateUrl: './alert-component.component.html',
  styleUrls: ['./alert-component.component.sass']
})
export class AlertComponentComponent implements OnInit {
  @Input() data;
  btnText = {success: '', cancel: ''};
  public modal: NgbModalRef;
  alertData: any = null;

  constructor(@Inject(TITLE) private injectedModalData: any) {

  }

  ngOnInit() {
    this.alertData = this.injectedModalData.data;
    this.modal = this.injectedModalData.modal;
    if (this.injectedModalData.data.btnText) this.btnText = this.injectedModalData.data.btnText;
  }

}
